import { Marker } from "./marker";

const controlName = "markercontainer";

export class MarkerContainerPanel {
    #element;

    #markers:Marker[] = [];
    #baseLuminanceMarker;

    constructor(){
        this.#element = this.#createElement(); 
        this.#baseLuminanceMarker = new Marker();
        this.#baseLuminanceMarker.element.classList.add("baseluminance");
        this.#baseLuminanceMarker.name = "baseLuminance";
        this.#baseLuminanceMarker.type = "baseLuminance";
        this.#baseLuminanceMarker.visible = false;
        this.#element.appendChild(this.baseLuminanceMarker.element);
    }

    get element(){return this.#element;}

    #createElement(){
        const elem = document.createElement("div");
        elem.classList.add(controlName);

        return elem;
    }

    addMarker(marker:Marker){
        this.#markers.push(marker);
        this.#element.appendChild(marker.element);
    }

    removeMarker(marker:Marker){
        const index = this.#markers.findIndex(x=>x===marker);
        if(index !== -1){
            this.#markers.splice(index, 1);
            this.#element.removeChild(marker.element);
        }
    }

    clearMarker(){
        for(let i=0;i<this.markers.length;i++){
            const marker = this.markers[i];
            this.#element.removeChild(marker.element);
        }

        this.#markers = [];
    }

    hideAllCommonMarker(){
        for(let i=0;i<this.#markers.length;i++){
            const marker = this.#markers[i];
            marker.visible = false;
        }
    }
    showAllCommonMarker(){
        for(let i=0;i<this.#markers.length;i++){
            const marker = this.#markers[i];
            marker.visible = true;
        }
    }

    get markers(){return this.#markers;}
    get baseLuminanceMarker(){return this.#baseLuminanceMarker;}
}