import { PFMData } from "../tools/pfm-parser";
import { fetchPFM } from '../api/pfm-loader';
import { Settings } from './settings';
import { LumimapLiteUI } from "../controls/lumimaplite-ui";
import { FileSelectedDetail, FileSelectedEvent, FileUploadRequestedDetail } from "../evens-defs";
import * as FileUtils from "../tools/file-utils";
import * as DeviceUtils from "../tools/device-utils";
import { uploadJpeg } from "../api/file-uploader";


export class LumimapLite {
    #loadedFile?:File;
    #ui;

    constructor(element: HTMLDivElement) {
        this.#ui = new LumimapLiteUI(element);
    }

    async init(){
        this.#ui.onFileSelected = this.#onFileSelected;
        this.#ui.onFileShareRequested = this.#onFileShareRequested;
        this.#ui.onFileUploadRequested = this.#onUploadFileRequested;
        this.#ui.cameraEnabled = await DeviceUtils.isDeviceSupportsCamera();
    }

    /**  */
    async loadAsync(jpg: File) {
        //this.reset();

        const exif = await FileUtils.getExifInfo(jpg);
        if (!exif || !exif.Exif || !exif.Exif.ShutterSpeedValue) {
            alert("");
            return;
        }

        this.#ui.showLoading();

        try {
            let pfmData: PFMData;

            if (jpg.type == "image/jpeg") {
                pfmData = await fetchPFM(jpg);
            }
            else {
                throw new Error("unsupported file type.");
            }

            const luminanceData = pfmData.grayscale!
            const imageWidth = pfmData.width;
            const imageHeight = pfmData.height;

            await this.#ui.loadAsync(jpg, luminanceData, imageWidth, imageHeight);
        }
        catch (ex) {
            // error here
            if (ex instanceof Error) {
                console.error(ex.message);
                alert(ex.message);
            }
            else {
                // error text
                console.error(ex);
                alert(ex);
            }

        }
        finally{
            this.#ui.hideLoading();
        }
    }

    #onFileSelected = (detail:FileSelectedDetail) => {
        this.loadAsync(detail.selectedFile);
    }

    #onFileShareRequested= async (file:File)=>{
        
        try {
            // share navigation
            if(navigator.canShare && navigator.canShare({ files: [file] })){
                await navigator.share({
                files: [file],
                title: "",
                text: "/"
                });
            }
            // 
            else{
                const url = URL.createObjectURL(file);

                const a = document.createElement("a");
                a.href = url;
                a.download = file.name;

                // 
                const parent = document.body;
                parent.appendChild(a);

                // download!
                a.click();

                // cleanup
                parent.removeChild(a);
                URL.revokeObjectURL(url);
            }

        } catch (err) {
            if(err instanceof String){
                if(err.includes("Abort")) return;
            }
            alert("" + err);
        }
    }

    checkFileShareable(file:File){
        return (navigator.canShare && navigator.canShare({ files: [file] }));
    }

    #onUploadFileRequested(detail:FileUploadRequestedDetail){
        try{
            uploadJpeg(this.#loadedFile!, detail.title, detail.message, detail.luminanceContrast.x, detail.luminanceContrast.y);
        }
        catch(err){
            alert("" + err);
        }
    }
}