import { ImageTypes } from "./type-defs";

export interface ImagingParameter {
    "luminance": {max:number, min:number},
    "luminanceRatio":{max:number, min:number, baseLuminance:number},
    "illuminance":{max:number, min:number, ref:number}
}

export class Settings {
    initialImageType:ImageTypes = "original";
    imagingParameter:ImagingParameter = {
        "luminance": {max:1000, min:1},
        "luminanceRatio":{max:10, min:0, baseLuminance:200},
        "illuminance":{max:300, min:1, ref:0.5}
    };
}