// : src/main.ts
import * as THREE from 'three';
import { WebGLCanvas } from './webgl-canvas';
import { ImagingParameter } from '../core/settings';
import { ImageTypes } from '../core/type-defs';
import { MarkerContainerPanel } from './marker-container';
import { Marker } from './marker';
import { Point, Rectangle, Size } from '../core/common';
import { EventHandler, MarkerEventHandler } from '../evens-defs';

type ObjectTypes = "marker" | "view" | "pinch" | "none";
type GestureTypes = "none" | "pan" | "pinch";
export type ViewClickActions = "createMarker" | "moveLuminanceMarker";
export type ViewMode = "createMarker" | "moveLuminanceMarker";

function createMarkerMovedEvent(marker:Marker){
    return new CustomEvent("markermoved", {detail:{marker:marker}});
}
function createMarkerCreatedEvent(marker:Marker){
    return new CustomEvent("markercreated", {detail:{marker:marker}});
}

export class MainView {
    #element;

    #activePointers = new Map<number, PointerEvent>();

    #pointedClientLocation = new Point();
    #initialMarkerPosition = new Point();
    #initialImageLocation = new Point();
    #initialZoom: number = 1;

    #initialPinchDistance = 0;
    #activeObjectType: ObjectTypes = "none";
    #gestureType: GestureTypes = "none";
    #clickAction: ViewClickActions = "createMarker";
    #viewMode:ViewMode = "createMarker";

    #activeMarker: Marker | null = null;

    #markerCreatable = true;
    #markerVisible = true;

    //#inputContoller;
    #imageSize = new Size();
    #markerContainer = new MarkerContainerPanel();
    #webGLCanvas = new WebGLCanvas();

    #resizeObserver;

    constructor() {
        this.#element = createElement();
        this.#element.appendChild(this.#webGLCanvas.element);
        this.#element.appendChild(this.#markerContainer.element);

        // 
        this.#element.addEventListener('wheel', this.#onWheel);
        // 
        // elem.addEventListener('touchstart', this.#onTouchStart, { passive: false });
        // elem.addEventListener('touchmove', this.#onTouchMove, { passive: false });
        // elem.addEventListener('touchend', this.#onTouchEnd);
        // elem.addEventListener('touchcancel', this.#onTouchEnd);

        this.#element.addEventListener("pointerdown", this.#onDown, { passive: false });
        this.#element.addEventListener("pointermove", this.#onMove, { passive: false });
        this.#element.addEventListener("pointerup", this.#onUp, { passive: false });
        this.#element.addEventListener("pointerleave", this.#onUp, { passive: false });
        this.#element.addEventListener("touchend", this.#onTouchEnd, { passive: false });
        //this.#inputContoller = new InputController(this)
        //this.#element.addEventListener("click", this.#onClick);

        // define resize observer;
        this.#resizeObserver = new ResizeObserver(entries => {
            for (const entry of entries) {
                if (entry.target == this.#element) {
                    this.#onResize();
                }
            }
        });
        this.#resizeObserver.observe(this.#element);

        // 
        this.#onResize();


        function createElement() {
            const view = document.createElement("div");
            view.classList.add("mainview");

            return view;
        }
    }

    initializeView(imageWidth: number, imageHeight: number) {
        this.#imageSize = new Size(imageWidth, imageHeight);

        this.#webGLCanvas.resetView();
        this.#webGLCanvas.setImageSize(imageWidth, imageHeight);
        this.#markerContainer.clearMarker();
        this.#markerContainer.baseLuminanceMarker.visible = false;
    }

    getBaseLuminanceMarker() {
        return this.#markerContainer.baseLuminanceMarker;
    }

    setBaseLuminanceMarkerImagePosition(imagePoint: Point) {
        this.#markerContainer.baseLuminanceMarker.imagePoint = imagePoint;
        this.#updateMarkerPosition(this.#markerContainer.baseLuminanceMarker);
        this.#emitOnMarkerMoved(this.#markerContainer.baseLuminanceMarker);
    }
    updateBaseLuminanceMarkerPosition() {
        this.#updateMarkerPosition(this.#markerContainer.baseLuminanceMarker);
    }

    get markers() { return this.#markerContainer.markers; }
    get element() { return this.#element; }
    get zoom() { return this.#webGLCanvas.zoom; }
    set zoom(value: number) {
        this.#webGLCanvas.zoom = value;
        this.#updateMarkerPositions();
    }
    // get clickAction(){return this.#clickAction;}
    // set clickAction(action:ViewClickActions){this.#clickAction = action;}

    get viewMode(){return this.#viewMode;}
    set viewMode(mode:ViewMode){
        this.#viewMode = mode;
        this.#updaterMarkerVisible();
    }

    get markerVisible(){return this.#markerVisible;}
    set markerVisible(tf:boolean){
        this.#markerVisible = tf;
        this.#updaterMarkerVisible();
    }

    #updaterMarkerVisible(){
        switch(this.#viewMode){
            case "createMarker":
                if(this.#markerVisible){
                    this.#markerContainer.showAllCommonMarker();
                    this.#markerCreatable = true;
                }
                else{
                    this.#markerContainer.hideAllCommonMarker();
                    this.#markerCreatable = false;
                }
                break;
            case "moveLuminanceMarker":
                this.#markerContainer.hideAllCommonMarker();
                this.#markerCreatable = false;
                break;
        }
    }

    zoomAt(zoom: number, offsetX: number, offsetY: number) {
        this.#webGLCanvas.zoomAt(zoom, offsetX, offsetY);
        this.#updateMarkerPositions();
    }

    startRender() {
        this.#webGLCanvas.startRender();
    }

    setImage(material: THREE.Material) {
        this.#webGLCanvas.material = material;
    }

    get baseMarkerVisible() {
        return this.#markerContainer.baseLuminanceMarker.visible;
    }
    set baseMarkerVisible(tf: boolean) {
        this.#markerContainer.baseLuminanceMarker.visible = tf;
    }

    showImage(material: THREE.Material) {
        this.#webGLCanvas.material = material;
    }

    #onResize() {
        const width = this.#element.clientWidth;
        const height = this.#element.clientHeight;
        this.#webGLCanvas.setSize(width, height);
        this.#updateMarkerPositions();
    }

    cameraPosition() {
        return this.#webGLCanvas.cameraPosition;
    }
    getDiffAppliedCameraPosition(cameraX: number, cameraY: number, screenDiffX: number, screenDiffY: number) {
        return this.#webGLCanvas.getDiffAppliedCameraPosition(cameraX, cameraY, screenDiffX, screenDiffY);
    }
    setCameraPosition(x: number, y: number) {
        this.#webGLCanvas.cameraPosition = new Point(x, y);
        this.#updateMarkerPositions();
    }

    resetPosition() {
        this.#webGLCanvas.resetView();
    }

    offsetToImagePoint(offsetX: number, offsetY: number) {
        return this.#webGLCanvas.offsetToImagePoint(offsetX, offsetY);
    }
    imagePointToOffset(imageX: number, imageY: number) {
        return this.#webGLCanvas.imagePointToOffset(imageX, imageY);
    }

    addMarker = (marker: Marker) => {
        this.#updateMarkerPosition(marker);
        this.#markerContainer.addMarker(marker);
        //this.#webGLCanvas.addMarkerFromOffset(marker.location.x, marker.location.y);
    }
    removeMarker = (marker: Marker) => {
        this.#markerContainer.removeMarker(marker);
        //this.#webGLCanvas.addMarkerFromOffset(marker.location.x, marker.location.y);
    }

    #updateMarkerPosition(marker: Marker) {
        marker.location = this.#webGLCanvas.imagePointToOffset(marker.imagePoint.x, marker.imagePoint.y);
    }

    #updateMarkerPositions() {
        // 
        const displayedImageRectangle = this.#webGLCanvas.getDisplayedImageRectangle();

        const markers = this.#markerContainer.markers;
        for (let i = 0; i < markers.length; i++) {
            const marker = markers[i];
            marker.location = this.#webGLCanvas.imagePointToOffset(marker.imagePoint.x, marker.imagePoint.y, displayedImageRectangle); //this.#getOffsetPoint(displayedImageRectangle, marker.imagePoint);
        }
        const baseLuminanceMarker = this.#markerContainer.baseLuminanceMarker;
        baseLuminanceMarker.location = this.#webGLCanvas.imagePointToOffset(baseLuminanceMarker.imagePoint.x, baseLuminanceMarker.imagePoint.y, displayedImageRectangle);
    }

    #createMarker(location: Point, imagePoint: Point) {
        const marker = new Marker();
        marker.location = location;
        marker.imagePoint = imagePoint;

        return marker;
    }

    #findMarker(markerElem: HTMLElement) {
        let marker = this.#markerContainer.markers.find(x => x.element == markerElem);
        if (marker) return marker;

        const baseLuminanceMarker = this.#markerContainer.baseLuminanceMarker;
        if (markerElem === baseLuminanceMarker.element) {
            return baseLuminanceMarker;
        }

        return null;
    }

    #onWheel = (e: WheelEvent) => {
        e.preventDefault();
        if (e.deltaY > 0) {
            this.zoomAt(this.#webGLCanvas.zoom - 0.5, e.offsetX, e.offsetY);
        } else {
            this.zoomAt(this.#webGLCanvas.zoom + 0.5, e.offsetX, e.offsetY);
            //this.zoom += 0.5;
        }
    }

    #onDown = (e: PointerEvent) => {
        e.stopPropagation();

        // 
        this.#activePointers.set(e.pointerId, e);

        //  pointerup 
        try { (e.target as Element).setPointerCapture?.(e.pointerId); } catch (err) { /* ignore */ }

        switch (this.#activePointers.size) {
            case 1:
                e.preventDefault?.();
                this.#gestureType = "pan";

                // 
                if (!this.#webGLCanvas.isOnImage(e.clientX, e.clientY)) return;

                // 
                const elem = (e.target as HTMLElement);
                if (elem.classList.contains("markercontainer")) {
                    this.#activeObjectType = "view";
                }
                else if (elem.classList.contains("finder")) {
                    this.#activeObjectType = "marker";
                    const markerElem = elem.closest(".marker") as HTMLElement;
                    const marker = this.#findMarker(markerElem);
                    if (!marker) return;

                    this.#activeMarker = marker;
                }
                else {
                    // 
                    this.#activeObjectType = "none";
                    this.#activePointers.delete(e.pointerId);
                    try { (e.target as Element).releasePointerCapture?.(e.pointerId); } catch (err) { }
                    return;
                }

                this.#pointedClientLocation = new Point(e.clientX, e.clientY);
                switch (this.#activeObjectType) {
                    case "view":
                        //this.#initialCameraPosition = this.cameraPosition();
                        this.#initialImageLocation = this.#webGLCanvas.imageLocation;
                        break;
                    case "marker":
                        this.#initialMarkerPosition = this.#activeMarker!.location;
                        break;
                }
                break;

            case 2:
                e.preventDefault?.();
                this.#gestureType = "pinch";
                const [p1, p2] = Array.from(this.#activePointers.values());
                this.#initialPinchDistance = Math.hypot(p2.clientX - p1.clientX, p2.clientY - p1.clientY);
                this.#initialZoom = this.#webGLCanvas.zoom; // WebGLCanvas
                this.#activeObjectType = "pinch";
                break;

            default:
                this.#gestureType = "none";
                break;
        }

        // 1
        if (!this.#webGLCanvas.isOnImage(e.clientX, e.clientY)) return;

        const elem = (e.target as HTMLElement);
        if (elem.classList.contains("markercontainer")) {
            this.#activeObjectType = "view";
        }
        else if (elem.classList.contains("finder")) {
            this.#activeObjectType = "marker";
            const markerElem = elem.closest(".marker") as HTMLElement;
            const marker = this.#findMarker(markerElem);
            if (!marker) return;

            this.#activeMarker = marker;
        }
        else {
            // 
            this.#activePointers.delete(e.pointerId);
            try { (e.target as Element).releasePointerCapture?.(e.pointerId); } catch (err) { }
            return;
        }



        // this.#pointedClientLocation = new Point(e.clientX, e.clientY);
        // switch (this.#activeObjectType) {
        //     case "view":
        //         this.#initialCameraPosition = this.cameraPosition();
        //         this.#initialImageLocation = this.#webGLCanvas.imageLocation;
        //         break;
        //     case "marker":
        //         this.#initialMarkerPosition = this.#activeMarker!.location;
        //         break;
        // }
    }

    #onMove = (e: PointerEvent) => {
        e.preventDefault?.();
        e.stopPropagation();

        // 
        if (this.#activePointers.has(e.pointerId)) {
            this.#activePointers.set(e.pointerId, e);
        }

        switch (this.#gestureType) {
            case "pan":
                const dx = e.clientX - this.#pointedClientLocation.x;
                const dy = e.clientY - this.#pointedClientLocation.y;

                switch (this.#activeObjectType) {
                    case "view":
                        this.#webGLCanvas.imageLocation = new Point(this.#initialImageLocation.x + dx, this.#initialImageLocation.y + dy);
                        this.#updateMarkerPositions();
                        break;

                    case "marker":
                        const newLocation = new Point(this.#initialMarkerPosition.x + dx, this.#initialMarkerPosition.y + dy);
                        let imagePoint = this.#webGLCanvas.offsetToImagePoint(newLocation.x, newLocation.y);
                        imagePoint = new Point(
                            Math.max(0, Math.min(imagePoint.x, this.#imageSize.width - 1)),
                            Math.max(0, Math.min(imagePoint.y, this.#imageSize.height - 1)),
                        );
                        const marker = this.#activeMarker!;
                        marker.imagePoint = imagePoint;
                        this.#updateMarkerPosition(marker);
                        this.#emitOnMarkerMoved(marker);
                        break;

                    default:
                        break;
                }
                break;
            case "pinch":
                const [p1, p2] = Array.from(this.#activePointers.values());
                const distance = Math.hypot(p2.clientX - p1.clientX, p2.clientY - p1.clientY);
                const scale = distance / this.#initialPinchDistance;
                this.#webGLCanvas.zoom = this.#initialZoom * scale;
                this.#updateMarkerPositions?.();
                break;
        }
    }

    #onUp = (e: PointerEvent) => {
        e.stopPropagation();
        e.preventDefault?.();
        // 
        this.#activePointers.delete(e.pointerId);

        // releasePointerCapture
        try { (e.target as Element).releasePointerCapture?.(e.pointerId); } catch (err) { }

        switch (this.#gestureType) {
            case "pinch":
                break;

            case "pan":
                // 
                const threshold = 4;
                const isOnClick = (this.#pointedClientLocation.x - threshold <= e.clientX && e.clientX <= this.#pointedClientLocation.x + threshold &&
                    this.#pointedClientLocation.y - threshold <= e.clientY && e.clientY <= this.#pointedClientLocation.y + threshold);

                switch (this.#activeObjectType) {
                    case "view":
                        if (isOnClick) this.#onClick(e);
                        break;

                    case "marker":
                        if (isOnClick) this.#onMarkerClick(this.#activeMarker!, e);
                        break;
                }

        }

        this.#gestureType = "none";
        this.#activeObjectType = "none";
        this.#activeMarker = null;
    }

    #onTouchEnd=(e:TouchEvent)=>{
        e.preventDefault?.();
    }

    #onClick = (e: PointerEvent) => {
        if (this.#imageSize.isEmpty) return;

        const imagePoint = this.#webGLCanvas.offsetToImagePoint(e.offsetX, e.offsetY)

        if (imagePoint.x < 0 || this.#imageSize.width <= imagePoint.x || imagePoint.y < 0 || this.#imageSize.height <= imagePoint.y) {
            return;
        }

        const location = this.#webGLCanvas.imagePointToOffset(imagePoint.x, imagePoint.y);
        let marker:Marker;
        switch (this.#viewMode) {
            case "createMarker":
                if(this.#markerCreatable && this.#markerVisible) {
                    marker = this.#createMarker(location, imagePoint);
                    this.onMarkerCreated?.(createMarkerCreatedEvent(marker));
                    this.#markerContainer.addMarker(marker);
                }
                break;
            case "moveLuminanceMarker":
                marker = this.#markerContainer.baseLuminanceMarker;
                marker.location = location;
                marker.imagePoint = imagePoint;
                this.onMarkerMoved?.(createMarkerMovedEvent(marker));
                break;
        }
    }

    createMarker(imagePoint: Point) {
        const location = this.#webGLCanvas.imagePointToOffset(imagePoint.x, imagePoint.y);
        const marker = this.#createMarker(location, imagePoint);
        return marker;
    }

    #onMarkerClick = (marker: Marker, e: PointerEvent) => {
        if (marker.type == "baseLuminance") return;

        this.#markerContainer.removeMarker(marker);
    }

    #emitOnMarkerMoved(marker: Marker) {
        this.onMarkerMoved?.(createMarkerMovedEvent(marker));
    }

    onClick: ((e: PointerEvent) => void) | null = null;
    onMarkerCreated: MarkerEventHandler | null = null;
    onMarkerMoved: MarkerEventHandler | null = null;
    onBaseLuminanceMarkerLoaded: MarkerEventHandler | null = null;
    onCameraMoved: EventHandler | null = null;
    onCameraZoomed: EventHandler | null = null;
}
