import { unzipSync } from "fflate";
import { PFMParser } from "../tools/pfm-parser";

interface FileInfo {
    Title:string,
    Message:string,
    LuminanceContrast:{
        X:number,
        Y:number
    }
};

const apiURL = 'https://ik1-127-70116.vs.sakura.ne.jp/markAnomaly';

export async function uploadJpeg(jpg:File, title:string, message:string, x:number, y:number){

    const info:FileInfo = {
        Title:title,
        Message:message,
        LuminanceContrast:{
            X:x,
            Y:y
        }
    }

    const formData = new FormData();
    formData.append("file", jpg);
    formData.append("info",  JSON.stringify(info));

    return await fetch(apiURL, {
        method: "POST",
        //body: JSON.stringify({ file1: file }),
        body: formData
    })
    .then(response =>{
        if (!response.ok) {
            throw new Error(`HTTP error! Status: ${response.status}`);
        }
    })
    .catch(e=>{
        throw new Error('Failed to fetch file');
    });
}