import { unzipSync } from "fflate";
import { PFMParser } from "../tools/pfm-parser";

const apiURL = 'https://ik1-127-70116.vs.sakura.ne.jp/markAnomaly';

export async function fetchPFM(jpg:File){
    const formData = new FormData();
    formData.append("file", jpg);

    return await fetch(apiURL, {
        method: "POST",
        //body: JSON.stringify({ file1: file }),
        body: formData
    })
    .then(response =>{
        if (!response.ok)  throw new Error(`HTTP error! Status: ${response.status}`);
        return response.arrayBuffer();
    })
    .then(buffer => unzipSync(new Uint8Array(buffer)))
    .then(zip =>{
        for (const [name, data] of Object.entries(zip) as [string, Uint8Array][]) {
            if(name.includes(".pfm"))
            {
                return PFMParser.Load(data);
            }
        }
        throw new Error('Could not found PFM File');
    })
    .catch(e=>{
        throw new Error('Failed to fetch file');
    });
}