import { ImagingParameter, Settings } from "../core/settings";
import { ImageTypes } from "../core/type-defs";
import { EventHandler, FileSelectedDetail, FileSelectedEventHandler, FileUploadRequestedDetail, FileUploadRequestedEventHandler, ImageSelectionChangedEventHandler, MarkerEvent } from "../evens-defs";
import { Button as Button } from "./button";
import { ControlBar } from "./controlbar";
import { ImageSelector } from "./image-selector";
import { MainView } from "./main-view";
import * as THREE from "three";
import { ScaleBarContainer } from "./scalebar-container";
import { ResourceManager } from "../core/resourcemanager";
import { Marker } from "./marker";
import * as Utils from "../core/utils";
import { Point } from "../core/common";
import { SelectFileButton } from "./select-file-button";
import { TextBoxPanel } from "./textbox-panel";
import { ToggleButton } from "./toggle-button";
import { IlluminanceControlBar } from "./illuminance-control-bar";
import { VideoView } from "./video-view";
import { LoadingView } from "./loading-view";
import { ConfirmDialog } from "./dialogs/confirm-dialog";
import { ImageView } from "./image-view";
import { UploadFileDialog } from "./dialogs/upload-file-dialoag";

const selectableImageLists: ImageTypes[] = ["original", "luminance", "luminanceRatio", "photometricOverflow"];

export class LumimapLiteUI {
    #settings = new Settings();
    #resourceManager = new ResourceManager();

    #element;

    #cameraEnabled = true;
    #fileSavable = true;

    //#menuBar;
    #showOriginToggleButton;
    #openFileButton;
    #uploadFileButton;
    #capturePhotoButton;
    #imageSelector;
    #markerVisibleToggleButton;
    #controlBar;

    #cameraView;
    #imageView;
    #mainView;
    #loadingView;
    
    #scaleBarGroup;
    #reflectionPanel;
    #illuminanceControlBar;

    #confirmDialog;
    #uploadFileDialog;

    get element() { return this.#element; }
    get controlBar() { return this.#controlBar; }
    get imageSelector() { return this.#imageSelector; }
    get mainView() { return this.#mainView; }
    get cameraEnabled() { return this.#cameraEnabled; }
    set cameraEnabled(enabled: boolean) {
        this.#cameraEnabled = enabled;
        if (enabled) {
            if (!this.#capturePhotoButton.element.parentElement) {
                this.#element.appendChild(this.#capturePhotoButton.element);
            }
        }
        else {
            if (this.#capturePhotoButton.element.parentElement) {
                this.#capturePhotoButton.element.parentElement.removeChild(this.#capturePhotoButton.element);
            }
        }
    }

    get fileSavable() { return this.#fileSavable };
    set fileSavable(savable: boolean) {

    }

    constructor(element: HTMLDivElement) {
        this.#element = element;

        //this.#menuBar = new MenuBar();
        this.#controlBar = new ControlBar();
        this.#cameraView = new VideoView();
        this.#cameraView.visible = false;
        this.#imageView = new ImageView();
        this.#imageView.visible = false;
        this.#loadingView = new LoadingView();
        this.#mainView = new MainView();
        this.#mainView.startRender();
        this.#imageSelector = new ImageSelector();
        this.#imageSelector.visible = false;
        this.#scaleBarGroup = new ScaleBarContainer();
        this.#openFileButton = new SelectFileButton("file-select-button");
        this.#uploadFileButton = new Button("upload-file-button");
        this.#capturePhotoButton = new Button("shootcambutton");
        this.#showOriginToggleButton = new ToggleButton("show-origin-toggle-button");
        this.#showOriginToggleButton.visible = false;
        this.#markerVisibleToggleButton = new ToggleButton("marker-visible-toggle-button");
        this.#markerVisibleToggleButton.visible = false;
        this.#markerVisibleToggleButton.checked = true;
        this.#reflectionPanel = new TextBoxPanel("reflectionpanel");
        this.#reflectionPanel.visible = false;
        this.#illuminanceControlBar = new IlluminanceControlBar();
        this.#illuminanceControlBar.visible = false;
        this.#uploadFileButton.visible = false;

        this.#confirmDialog = new ConfirmDialog();
        this.#uploadFileDialog = new UploadFileDialog();

        //this.#element.appendChild(this.#menuBar.element);
        this.#element.appendChild(this.#controlBar.element);
        this.#element.appendChild(this.#mainView.element);
        this.#element.appendChild(this.#imageSelector.element);
        this.#element.appendChild(this.#openFileButton.element);
        this.#element.appendChild(this.#capturePhotoButton.element);
        this.#element.appendChild(this.#showOriginToggleButton.element);
        this.#element.appendChild(this.#scaleBarGroup.element);
        this.#element.appendChild(this.#markerVisibleToggleButton.element);
        this.#element.appendChild(this.#reflectionPanel.element);
        this.#element.appendChild(this.#illuminanceControlBar.element);
        this.#element.appendChild(this.#uploadFileButton.element);

        this.#element.appendChild(this.#cameraView.element);
        this.#element.appendChild(this.#imageView.element);

        this.#element.appendChild(this.#confirmDialog.element);
        this.#element.appendChild(this.#uploadFileDialog.element);

        this.#scaleBarGroup.luminanceScaleBar.onMaxChanged = this.#onLuminanceScalebarMaxChanged;
        this.#scaleBarGroup.luminanceScaleBar.onMinChanged = this.#onLuminanceScalebarMinChanged;
        this.#scaleBarGroup.illuminanceScaleBar.onMaxChanged = this.#onIlluminanceScalebarMaxChanged;
        this.#scaleBarGroup.illuminanceScaleBar.onMinChanged = this.#onIlluminanceScalebarMinChanged;
        this.#scaleBarGroup.illuminanceScaleBar.onReflectionChanged = this.#onIlluminanceScalebarReflectionChanged;
        this.#openFileButton.onFileSelected = this.#onFileSelected;
        this.#mainView.onMarkerCreated = this.#onMarkerCreated;
        this.#mainView.onMarkerMoved = this.#onMarkerMoved;
        this.#mainView.onBaseLuminanceMarkerLoaded = this.#onBaseLuminanceMarkerLoaded;
        this.#imageSelector.items = selectableImageLists;
        this.#imageSelector.onSelectionChanged = this.#onImageSelectionChanged;
        this.#showOriginToggleButton.checked = false;
        this.#showOriginToggleButton.visible = false;
        this.#showOriginToggleButton.onCheckedChanged = this.#onShowOriginImageCheckedChanged;
        this.#markerVisibleToggleButton.onCheckedChanged = this.#onSwitchMarkerVisibleCheckedChanged;
        this.#capturePhotoButton.onClick = this.#onCapturePhotoButtonClick;
        this.#uploadFileButton.onClick = this.#onUploadFileButtonClick;
        this.#cameraView.onCaptured = this.#onCameraViewPhotoCaptured;

        // 
        this.#element.addEventListener("touchend", (e)=>{
            e.preventDefault();
        }, 
        {passive:false});
    }

    /**  */
    showLoading() {
        this.#loadingView.open(this.element);
    }
    /**  */
    hideLoading() {
        this.#loadingView.close();
    }

    async loadAsync(jpeg: File, luminanceData: Float32Array, width: number, height: number) {
        const r = this.#resourceManager;
        await r.loadAsync(jpeg, luminanceData, width, height);
        this.#setupMaterialParameter(this.#settings.imagingParameter);
        this.#setupScaleBarParameter(this.#settings.imagingParameter);
        this.#updateIlluminanceReflectionText();
        this.#imageSelector.visible = true;
        this.#markerVisibleToggleButton.visible = true;
        this.#uploadFileButton.visible = true;

        // MainView
        this.#mainView.initializeView(r.imageWidth, r.imageHeight);
        this.#mainView.setBaseLuminanceMarkerImagePosition(this.getInitialBaseLuminanceMarkerImagePosition());
        const material = r.materials.get(this.#settings.initialImageType);
        if (material) this.#showImage(this.#settings.initialImageType, material);
    }

    #setupMaterialParameter(ip: ImagingParameter) {
        const m = this.#resourceManager.materials;
        if (m.has("luminance")) {
            const luminance = m.get("luminance") as THREE.ShaderMaterial;
            luminance.uniforms.uMax.value = ip.luminance.max;
            luminance.uniforms.uMin.value = ip.luminance.min;
        }
        if (m.has("illuminance")) {
            const illuminance = m.get("illuminance") as THREE.ShaderMaterial
            illuminance.uniforms.uMax.value = ip.illuminance.max;
            illuminance.uniforms.uMin.value = ip.illuminance.min;
            illuminance.uniforms.uRefRatio.value = ip.illuminance.ref;
        }
        if (m.has("luminanceRatio")) {
            const luminanceRatio = m.get("luminanceRatio") as THREE.ShaderMaterial;
            luminanceRatio.uniforms.uMax.value = ip.luminanceRatio.max;
            luminanceRatio.uniforms.uMin.value = ip.luminanceRatio.min;
            luminanceRatio.uniforms.uBaseLm.value = ip.luminanceRatio.baseLuminance;
        }
    }

    #setupScaleBarParameter(ip: ImagingParameter) {
        const luminance = this.#scaleBarGroup.luminanceScaleBar;
        luminance.min = ip.luminance.min;
        luminance.max = ip.luminance.max;

        const illuminance = this.#scaleBarGroup.illuminanceScaleBar;
        illuminance.min = ip.illuminance.min;
        illuminance.max = ip.illuminance.max;
        illuminance.reflection = ip.illuminance.ref;
    }

    #updateIlluminanceReflectionText() {
        const ref = this.#scaleBarGroup.illuminanceScaleBar.reflection;
        this.#reflectionPanel.text = ref.toFixed(4);
    }

    // 
    #showImage(imageType: ImageTypes, material: THREE.Material) {
        this.#imageSelector.selectedImageType = imageType;
        this.#scaleBarGroup.showScalebar(imageType);
        this.#mainView.setImage(material);

        switch (imageType) {
            case "luminanceRatio":
            case "illuminance":
                this.#showOriginToggleButton.visible = true;
                break;
            default:
                this.#showOriginToggleButton.visible = false;
                break;

        }
        this.#showOriginToggleButton.checked = false;
    }

    /**   */
    getInitialBaseLuminanceMarkerImagePosition() {
        const image = this.#resourceManager.images.get("luminance")!;

        // 
        // y
        let centerX = Math.floor(image.width / 2);
        let centerY = Math.floor(image.height / 2);
        let val = 0;

        let x = centerX, y = centerY;
        let left = centerX, right = centerX, top = centerY, bottom = centerY;

        let p = new Point(centerX, centerY);
        if (!Utils.isOverOrUnderflow(image.getValue(p.x, p.y))) return p;
        while (0 <= left || right < image.width || 0 <= top || bottom < image.height) {

            right++;
            if (0 <= y) {
                for (; x <= right; x++) {
                    if (image.width <= x) break;

                    val = image.getValue(x, y);
                    if (!Utils.isOverOrUnderflow(val)) return new Point(x, y);
                }
            }
            x = right;

            bottom++;
            if (x < image.width) {
                for (; y <= bottom; y++) {
                    if (image.height <= y) break;

                    val = image.getValue(x, y);
                    if (!Utils.isOverOrUnderflow(val)) return new Point(x, y);
                }
            }
            y = bottom;

            left++;
            if (y < image.height) {
                for (; x >= left; x--) {
                    if (x < 0) break;

                    val = image.getValue(x, y);
                    if (!Utils.isOverOrUnderflow(val)) return new Point(x, y);
                }
            }
            x = left;


            top++;
            if (0 <= x) {
                for (; y >= top; y--) {
                    if (y < 0) break;
                    val = image.getValue(x, y);
                    if (!Utils.isOverOrUnderflow(val)) return new Point(x, y);
                }
            }
            y = top;
        }

        // 
        return new Point(centerX, centerY);
    }

    #emitFileSelected(file: File) {
        this.onFileSelected?.({ selectedFile: file });
    }


    #onFileSelected = async (e: Event) => {
        if (this.#openFileButton.selectedFile) {
            this.#emitFileSelected(this.#openFileButton.selectedFile);
        }
    }

    #onLuminanceScalebarMinChanged = (e: Event) => {
        const material = this.#resourceManager.materials.get("luminance")! as THREE.ShaderMaterial;
        material.uniforms.uMin.value = this.#scaleBarGroup.luminanceScaleBar.min;
    }
    #onLuminanceScalebarMaxChanged = (e: Event) => {
        const material = this.#resourceManager.materials.get("luminance")! as THREE.ShaderMaterial;
        material.uniforms.uMax.value = this.#scaleBarGroup.luminanceScaleBar.max;
    }
    #onIlluminanceScalebarMaxChanged = (e: Event) => {
        const material = this.#resourceManager.materials.get("illuminance")! as THREE.ShaderMaterial;
        material.uniforms.uMax.value = this.#scaleBarGroup.illuminanceScaleBar.max;
    }
    #onIlluminanceScalebarMinChanged = (e: Event) => {
        const material = this.#resourceManager.materials.get("illuminance")! as THREE.ShaderMaterial;
        material.uniforms.uMin.value = this.#scaleBarGroup.illuminanceScaleBar.min;
    }

    #onIlluminanceScalebarReflectionChanged = (e: Event) => {
        const material = this.#resourceManager.materials.get("illuminance")! as THREE.ShaderMaterial;
        this.#updateIlluminanceReflectionText();
        material.uniforms.uRefRatio.value = this.#scaleBarGroup.illuminanceScaleBar.reflection;;
    };

    #onImageSelectionChanged = (e: Event) => {
        const type = this.imageSelector.selectedImageType;

        this.#mainView.setImage(this.#resourceManager.materials.get(type)!);

        this.#reflectionPanel.visible = false;
        this.#mainView.baseMarkerVisible = false;
        this.#showOriginToggleButton.visible = false;
        this.#illuminanceControlBar.visible = false;
        switch (type) {
            case "luminanceRatio":
                this.#mainView.baseMarkerVisible = true;
                this.#showOriginToggleButton.visible = true;
                break;
            case "illuminance":
                this.#mainView.baseMarkerVisible = true;
                this.#showOriginToggleButton.visible = true;
                this.#reflectionPanel.visible = true;
                this.#illuminanceControlBar.visible = true;
                break;
            default:
                break;
        }


        this.#updateAllMarkerTips();

        this.#scaleBarGroup.showScalebar(type);
    }

    #onShowOriginImageCheckedChanged = (e: Event) => {
        let material;
        if (this.#showOriginToggleButton.checked) {
            material = this.#resourceManager.materials.get("original")!;
            this.#mainView.viewMode = "moveLuminanceMarker";
        }
        else {
            material = this.#resourceManager.materials.get(this.imageSelector.selectedImageType)!;
            this.#mainView.viewMode = "createMarker";
        }
        this.#mainView.setImage(material);
    }
    #onSwitchMarkerVisibleCheckedChanged = async (e: Event) => {
        if(this.#markerVisibleToggleButton.checked){
            this.#mainView.markerVisible = true;
        }
        else{
            this.#mainView.markerVisible = false;
        }
    }

    #onCapturePhotoButtonClick = async (e: Event) => {
        this.#cameraView.show();
    }

    #onCameraViewPhotoCaptured=async(file:File)=>{
        //const file = await this.#cameraView.capturePhoto(name);

        if (!file) return;
        this.#imageView.showImage(file);
        this.#imageView.visible = true;
        const dialog = this.#confirmDialog;
        dialog.title = "";
        dialog.message = "";

        const result = await dialog.showModal();
        if (result == "yes") {
            this.#saveFile(file);
            this.#cameraView.hide();
            this.#emitFileSelected(file);
        }
        this.#imageView.visible = false;
    }

    #onUploadFileButtonClick = async (e: Event) => {
        const dialog = this.#uploadFileDialog;
        dialog.title = "RealapsHub";
        
        const result = await dialog.showModal();

        if (result == "cancel") return;

        if (this.onFileUploadRequested) {
            const baseLuminanceMarkerImagePoint = this.#mainView.getBaseLuminanceMarker().imagePoint;
            const detail: FileUploadRequestedDetail = {
                title: dialog.titleText,
                message: dialog.message,
                luminanceContrast: {
                    x: baseLuminanceMarkerImagePoint.x / this.#resourceManager.imageWidth,
                    y: baseLuminanceMarkerImagePoint.y / this.#resourceManager.imageHeight,
                }
            }
            this.onFileUploadRequested(detail);
        }

    }

    #saveFile(file: File) {
        if (!this.#fileSavable) return;
        this.onFileShareRequested?.(file);
    }

    #onMarkerCreated = (e: MarkerEvent) => {
        this.#setMarkerTips(e.detail.marker);
    }

    #onMarkerMoved = (e: MarkerEvent) => {
        const marker = e.detail.marker;

        // 
        if (marker.name === "baseLuminance") {
            this.#settings.imagingParameter.luminanceRatio.baseLuminance = this.#resourceManager.images.get("luminance")?.getValue(marker.imagePoint.x, marker.imagePoint.y)!;
            this.#setLuminanceTips(marker);
            this.#updateAllMarkerTips();

            const mat = this.#resourceManager.materials.get("luminanceRatio")! as THREE.ShaderMaterial;
            mat.uniforms.uBaseLm.value = this.#settings.imagingParameter.luminanceRatio.baseLuminance;
        }
        else {
            this.#setMarkerTips(marker);
        }
    }

    #onBaseLuminanceMarkerLoaded = (e: MarkerEvent) => {
        this.#setLuminanceTips(e.detail.marker);
    }

    #updateAllMarkerTips() {
        for (let i = 0; i < this.mainView.markers.length; i++) {
            const marker = this.mainView.markers[i];
            this.#setMarkerTips(marker);
        }
    }

    #setMarkerTips(marker: Marker) {
        switch (this.imageSelector.selectedImageType) {
            case "original":
                this.#setOriginalTips(marker);
                break;
            case "luminance":
            case "photometricOverflow":
                this.#setLuminanceTips(marker);
                break;
            case "luminanceRatio":
                this.#setLuminanceRatioTips(marker);
                break;
            case "illuminance":
                this.#setIlluminanceTips(marker);
                break;
            default:
                this.#hideMarkerTips(marker);
                break;
        }
    }

    #setOriginalTips(marker: Marker) {
        this.#setLuminanceTips(marker);
    }

    #setLuminanceTips(marker: Marker) {
        const image = this.#resourceManager.images.get("luminance")!;
        let val = image.getValue(marker.imagePoint.x, marker.imagePoint.y);
        const p = this.#getLuminanceParagraph(val);
        this.#loadMarkerTips(marker, [p]);
    }
    #setLuminanceRatioTips(marker: Marker) {
        const image = this.#resourceManager.images.get("luminance")!;
        let val = image.getValue(marker.imagePoint.x, marker.imagePoint.y);
        let rows = [];
        const luminanceP = this.#getLuminanceParagraph(val);
        rows.push(luminanceP);
        if (!Utils.isOverOrUnderflow(val)) {
            const luminanceRatioP = this.#getLuminanceRatioParagraph(val);
            rows.push(luminanceRatioP);
        }
        this.#loadMarkerTips(marker, rows);
    }
    #setIlluminanceTips(marker: Marker) {
        const image = this.#resourceManager.images.get("luminance")!;
        let val = image.getValue(marker.imagePoint.x, marker.imagePoint.y);
        const illuminanceP = this.#getIlluminanceParagraph(val);
        this.#loadMarkerTips(marker, [illuminanceP]);
    }

    #getLuminanceParagraph(val: number) {
        let p = document.createElement("p");
        if (Utils.isOverflow(val)) {
            p.textContent = "";
        }
        else if (Utils.isUnderflow(val)) {
            p.textContent = "";
        }
        else {
            p.textContent = val.toFixed(2) + " cd/m";
            const sup = document.createElement("sup");
            sup.textContent = "2";
            p.appendChild(sup);
        }

        return p;
    }

    #getLuminanceRatioParagraph(val: number) {
        let p = document.createElement("p");
        if (Utils.isOverflow(val)) {
            p.textContent = "";
        }
        else if (Utils.isUnderflow(val)) {
            p.textContent = "";
        }
        else {
            p.textContent = (val / this.#settings.imagingParameter.luminanceRatio.baseLuminance).toFixed(2);
        }

        return p;
    }

    #getIlluminanceParagraph(luminance: number) {
        let p = document.createElement("p");
        if (Utils.isOverflow(luminance)) {
            p.textContent = "";
        }
        else if (Utils.isUnderflow(luminance)) {
            p.textContent = "";
        }
        else {
            p.textContent = ((luminance * Math.PI) / this.#scaleBarGroup.illuminanceScaleBar.reflection).toFixed(2) + " lx";
        }

        return p;
    }


    #loadMarkerTips(marker: Marker, rows: HTMLParagraphElement[]) {
        if (rows.length != 0) {
            let frag = document.createDocumentFragment();
            for (let i = 0; i < rows.length; i++) {
                frag.appendChild(rows[i]);
            }
            marker.setTipsContent(frag);
        }
        else {
            marker.setTipsContent(undefined);
        }
    }

    #hideMarkerTips(marker: Marker) {
        marker.tipsVisible = false;
    }

    onFileSelected?: (detail: FileSelectedDetail) => void;
    onFileShareRequested?: (file: File) => void;
    onFileUploadRequested?: (detail: FileUploadRequestedDetail) => void;

}