function craeteTitleElement(){
    const title = document.createElement("h4");
    title.classList.add("title");
    return title;
}
function createDialog(title:HTMLHeadingElement) {
    const dialog = document.createElement("dialog");

    dialog.appendChild(title);

    return dialog;
}

export class DialogBase {
    #element;
    #titleElement;

    #title = "";
    constructor(formContent?:HTMLFormElement) {
        this.#titleElement = craeteTitleElement();
        this.#element = createDialog(this.#titleElement);
        if(formContent){
            this.#element.appendChild(formContent);
        }
    }

    show() {
        this.#element.show();
    }

    async showModal() {
        return new Promise(resolve => {
            const onClose = () => {
                this.#element.removeEventListener("close", onClose);
                resolve(this.#element.returnValue);
            };
            this.#element.addEventListener("close", onClose);
            this.#element.showModal();
        });
    }

    close() {
        this.#element.close();
    }

    get value() {
        return this.#element.returnValue;
    }

    get title(){return this.#title;}
    set title(title:string){
        this.#title = title;
        this.#titleElement.textContent = title;
    }
    get element(){return this.#element;}
}