import * as THREE from 'three';
import { Point, Size, Rectangle } from '../core/common';
const DEFAULT_FOV = 45;
const MAX_ZOOM = 10.0;
const MIN_ZOOM = 1.0;
const MARKER_SIZE = 12; //px
const BACK_COLOR = "#222";
const PANEL_COLOR = "#2F4F4F";
const PIXEL_OFFSET = 0.5;
const CAMERA_PSOTION_Z = 10;

const controlName = "rendersurface";

export class WebGLCanvas {
    #clientSize = new Size();

    // 
    #imageSize = new Size(1, 1);

    #scene;
    #camera;
    #renderer;
    
    #planeGeometry;
    #planeMaterial;
    #planeMesh;

    //#markerMeshes:THREE.Mesh[] = [];

    constructor() {

        this.#scene = new THREE.Scene();

        this.#camera = new THREE.OrthographicCamera(-1, 1, 1, -1, 0.1, CAMERA_PSOTION_Z);
        this.#camera.position.z = 1;
        this.#renderer = new THREE.WebGLRenderer();
        this.#renderer.setClearColor(BACK_COLOR);
        this.#renderer.autoClear = true;
        this.#renderer.domElement.classList.add(controlName);

        // plane
        this.#planeGeometry = new THREE.PlaneGeometry(1, 1);
        this.#planeMaterial = new THREE.MeshBasicMaterial({ color: PANEL_COLOR });
        this.#planeMesh = new THREE.Mesh(this.#planeGeometry, this.#planeMaterial);


        // add to scene
        this.#scene.add(this.#planeMesh);
    }

    get camera() { return this.#camera; }
    get element() { return this.#renderer.domElement; }

    startRender() {
        this.render();
    }

    setSize(width: number, height: number) {
        this.#clientSize = new Size(width, height);

        this.#renderer.setSize(width, height, false);

        this.#updatePlaneSize();
        this.#updateCameraProjection();
    }

    #updateCameraProjection() {
        const viewSize = this.#getViewPortSize();

        const camera = this.#camera;
        camera.left = -viewSize.width / 2;
        camera.right = viewSize.width / 2;
        camera.top = viewSize.height / 2;
        camera.bottom = -viewSize.height / 2;
        camera.updateProjectionMatrix();
    }

    set material(material: THREE.Material) {
        this.#planeMesh.material = material as any;
        material.needsUpdate = true;
    }

    setImageSize(width: number, height: number) {
        this.#imageSize = new Size(width, height);
        this.#updatePlaneSize();
    }

    #updatePlaneSize() {
        const viewportSize = this.#getViewPortSize();
        const imageAspect = this.#imageSize.width / this.#imageSize.height;
        const viewportAspect = viewportSize.width / viewportSize.height;

        const planeSize = new Size(viewportSize.width, viewportSize.height);
        if (viewportAspect < imageAspect) { // 
            planeSize.width = viewportSize.width;
            planeSize.height = viewportSize.width * (this.#imageSize.height / this.#imageSize.width);
        }
        else if (imageAspect < viewportAspect) { // 
            planeSize.height = viewportSize.height;
            planeSize.width = viewportSize.height * (this.#imageSize.width / this.#imageSize.height);
        }

        //this.#planeMesh.scale.set(w, h, 1);
        this.#planeMesh.scale.set(planeSize.width, planeSize.height, 1);
    }


    get zoom() { return this.#camera.zoom; }
    set zoom(zoom: number) {
        const cameraZoom = this.#camera.zoom;
        const newZoom = this.getClampedZoom(zoom);
        if (cameraZoom == newZoom) return;

        this.#camera.zoom = newZoom;
        this.#camera.updateProjectionMatrix();
        if (this.#camera.zoom == 1) {
            this.cameraPosition = new Point(0, 0);
        }
    }

    zoomAt(zoom: number, clientX: number, clientY: number) {
        const recentImageRect = this.getDisplayedImageRectangle();

        if (clientX < recentImageRect.left || recentImageRect.right < clientX || clientY < recentImageRect.top || recentImageRect.bottom < clientY) {
            return;
        }

        const newZoom = this.getClampedZoom(zoom);
        if (this.zoom == newZoom) return;

        if (newZoom == 1) { }

        const imageX = clientX - recentImageRect.x;
        const imageY = clientY - recentImageRect.y;

        const imageCoordRatio = new Point(imageX / recentImageRect.width, imageY / recentImageRect.height);

        // update zoom
        this.zoom = newZoom;

        if (this.zoom != 1) {
            const newImageRect = this.getDisplayedImageRectangle();
            // 
            const imagePixelAtOffset = new Point(imageCoordRatio.x * newImageRect.width, imageCoordRatio.y * newImageRect.height);
            newImageRect.x = clientX - imagePixelAtOffset.x;
            newImageRect.y = clientY - imagePixelAtOffset.y;

            this.imageLocation = newImageRect.location;
        }
    }

    getClampedZoom(zoom: number) {
        return Math.max(MIN_ZOOM, Math.min(MAX_ZOOM, zoom));
    }

    get cameraPosition() { return new Point(this.#camera.position.x, this.#camera.position.y); };
    set cameraPosition(position: Point) {
        if (this.#camera.zoom == 1) {
            position.x = 0;
            position.y = 0;
        }
        this.#camera.position.x = position.x;
        this.#camera.position.y = position.y;
        this.#camera.updateMatrixWorld();
    }

    get imageLocation() {
        const dispRect = this.getDisplayedImageRectangle();
        return dispRect.location;
    }
    set imageLocation(location: Point) {
        const imageRect = this.getUnmovedDisplayedImageRectangle();

        // constraint bounds
        // 
        const newLocation = new Point(location.x, location.y);


        if (imageRect.width < this.#clientSize.width) {
            // const leftOut = location.x < 0, rightOut = this.#clientSize.width < location.x + imageRect.width;
            // if(leftOut){
            //     newLocation.x = 0;
            // }
            // else if(rightOut){
            //     newLocation.x = this.#clientSize.width - imageRect.width;
            // }
            newLocation.x = imageRect.x;
        }
        else {
            const insideLeft = 0 < location.x, insideRight = location.x + imageRect.width < this.#clientSize.width;
            if (insideLeft) {
                newLocation.x = 0;
            }
            else if (insideRight) {
                newLocation.x = this.#clientSize.width - imageRect.width;
            }
        }

        if (imageRect.height < this.#clientSize.height) {
            newLocation.y = imageRect.y;
        }
        else {
            const insideTop = 0 < location.y, insideBottom = location.y + imageRect.height < this.#clientSize.height;

            if (insideTop) {
                newLocation.y = 0;
            }
            else if (insideBottom) {
                newLocation.y = this.#clientSize.height - imageRect.height;
            }

        }


        const def = new Point(newLocation.x - imageRect.x, newLocation.y - imageRect.y);

        const viewSize = this.#getViewPortSize();

        // 
        const clientRatio = new Point(def.x / this.#clientSize.width, def.y / this.#clientSize.height);
        // 
        const worldPoint = new Point(clientRatio.x * viewSize.width, clientRatio.y * viewSize.height);
        // zoom
        const cameraPosition = new Point(-worldPoint.x / this.#camera.zoom, worldPoint.y / this.#camera.zoom);

        this.cameraPosition = cameraPosition;
    }

    // 1
    #getViewPortSize() {
        let viewSize = new Size(1, 1);

        if (this.#clientSize.width < this.#clientSize.height) {
            viewSize.height = this.#clientSize.height / this.#clientSize.width;
        }
        else if (this.#clientSize.width > this.#clientSize.height) {
            viewSize.width = this.#clientSize.width / this.#clientSize.height;
        }

        return viewSize;
    }


    render = () => {
        this.#renderer.render(this.#scene, this.#camera);

        requestAnimationFrame(this.render);
    }

    resetView() {
        this.zoom = MIN_ZOOM;
        this.cameraPosition = new Point(0, 0);
    }

    getDiffAppliedCameraPosition(cameraPosX: number, cameraPosY: number, clientDiffX: number, clientDiffY: number) {
        const viewSize = this.#getViewPortSize();
        const x = cameraPosX - (clientDiffX / this.#clientSize.width * viewSize.width) / this.#camera.zoom;
        const y = cameraPosY + (clientDiffY / this.#clientSize.height * viewSize.height) / this.#camera.zoom;

        return new Point(x, y);
    }

    offsetToImagePoint(offsetX: number, offsetY: number) {
        let displayedImageRectangle = this.getDisplayedImageRectangle();

        const offsetRatioInImage = new Point(
            (offsetX - displayedImageRectangle.x) / (displayedImageRectangle.width),
            (offsetY - displayedImageRectangle.y) / displayedImageRectangle.height
        );


        const imagePoint = new Point(
            Math.floor(this.#imageSize.width * offsetRatioInImage.x),
            Math.floor(this.#imageSize.height * offsetRatioInImage.y));
        return imagePoint;
    }

    offsetToWorldPoint(offsetX: number, offsetY: number) {

        const planeRectangle = new Rectangle(
            this.#camera.position.x, this.#camera.position.y,
            (this.#clientSize.width / this.#clientSize.height) / this.#camera.zoom, 1 / this.#camera.zoom);

        const offsetRatio = new Point(offsetX / this.#clientSize.width, offsetY / this.#clientSize.height);

        // world
        // (0,0)
        const worldPoint = new Point(
            planeRectangle.width * offsetRatio.x - planeRectangle.width / 2 + planeRectangle.x,
            planeRectangle.height / 2 - planeRectangle.height * offsetRatio.y + planeRectangle.y);

        return worldPoint;
    }

    imagePointToOffset(imageX: number, imageY: number, displayedImageRectangle?: Rectangle) {
        // 
        if (!displayedImageRectangle) displayedImageRectangle = this.getDisplayedImageRectangle();

        const imageRatio = new Point(
            (imageX + PIXEL_OFFSET) / (this.#imageSize.width),
            (imageY + PIXEL_OFFSET) / (this.#imageSize.height)
        );

        // 
        return new Point(
            displayedImageRectangle.x + imageRatio.x * displayedImageRectangle.width,
            displayedImageRectangle.y + imageRatio.y * displayedImageRectangle.height
        );
    }

    isOnImage(clientX:number, clientY:number){
        const imageRect = this.getDisplayedImageRectangle();
        return !(clientX < imageRect.x || imageRect.right < clientX || clientY < imageRect.top || imageRect.bottom < clientY);
    }

    /**  */
    getDisplayedImageRectangle() {
        const rect = this.getUnmovedDisplayedImageRectangle();

        if (this.#camera.zoom == 1) {
            // Zoom:1
        }
        else {
            // 
            const viewPortSize = this.#getViewPortSize();
            // displayedImageLocation.x -= (this.#camera.position.x / viewPortSize.width * canvasSize.width) * this.#camera.zoom;
            // displayedImageLocation.y += (this.#camera.position.y / viewPortSize.height * canvasSize.height) * this.#camera.zoom;

            rect.x -= (this.#camera.position.x / viewPortSize.width * this.#clientSize.width) * this.#camera.zoom;
            rect.y += (this.#camera.position.y / viewPortSize.height * this.#clientSize.height) * this.#camera.zoom;
        }

        //
        //this.#camera.position.x = (dx / this.#lastClientWidth * this.#viewWidth) / this.#camera.zoom;
        return rect;
    }

    /**  */
    getUnmovedDisplayedImageRectangle() {

        const imageSize = this.#imageSize;
        const imageAspect = imageSize.width / imageSize.height;
        const canvasSize = this.#clientSize;
        const canvasAspect = canvasSize.width / canvasSize.height;

        let width = canvasSize.width, height = canvasSize.height, x = 0, y = 0;

        // Zoom:1(px)
        if (imageAspect > canvasAspect) {
            // 
            width = canvasSize.width;
            height = canvasSize.width * (imageSize.height / imageSize.width);
            x = 0;
            y = (canvasSize.height - height) / 2;
        }
        else if (imageAspect < canvasAspect) {
            // 
            width = canvasSize.height * (imageSize.width / imageSize.height);
            height = canvasSize.height;
            x = (canvasSize.width - width) / 2;
            y = 0;
        }

        // zoom 
        width = width * this.#camera.zoom;
        height = height * this.#camera.zoom;
        x = (canvasSize.width - width) / 2;
        y = (canvasSize.height - height) / 2;

        //
        //this.#camera.position.x = (dx / this.#lastClientWidth * this.#viewWidth) / this.#camera.zoom;
        return new Rectangle(x, y, width, height);
    }

    getCameraPositionFromImageLocation(point: Point) {
        const imageRect = this.getUnmovedDisplayedImageRectangle();


        const def = new Point(imageRect.x - point.x, imageRect.y - point.y);
        const viewSize = this.#getViewPortSize();

        const clientSizeRatio = new Size(def.x / this.#clientSize.width, def.y / this.#clientSize.height);

        const worldPoint = new Point(clientSizeRatio.width * viewSize.width, clientSizeRatio.height * viewSize.height);
        const zoomedWorldPoint = new Point(worldPoint.x / this.#camera.zoom, worldPoint.y / this.#camera.zoom);
        console.log(zoomedWorldPoint)
        return zoomedWorldPoint;
    }
}