export class LumimapImageData{
    #width:number;
    #height:number;
    #data;    
    constructor(data:Float32Array, width:number, height:number){
        this.#data = data;
        this.#width = width;
        this.#height = height;
    }

    get width(){return this.#width;}
    get height(){return this.#height;}
    
    getValue(x:number, y:number){
        return this.#data[this.#width * y + x];
    }
    setValue(x:number, y:number, value:number){
        this.#data[this.#width * y + x] = value;
    }
}