export async function checkCameraEnabled() {
    // 1. ImageCapture 
    if (typeof (window as any).ImageCapture !== "function") {
        return false;
    }

    // 2. 
    try {
        const stream = await navigator.mediaDevices.getUserMedia({
            video: true
        });

        const track = stream.getVideoTracks()[0];
        if (!track) {
            return false;
        }

        // 3.  track  ImageCapture 
        try {
            const ic = new (window as any).ImageCapture(track);

            // takePhoto 
            const ok = typeof ic.takePhoto === "function";

            // 
            track.stop();

            return ok;
        } catch {
            track.stop();
            return false;
        }
    } catch {
        // 
        return false;
    }
}

export async function isDeviceSupportsCamera() {
    // 1. ImageCapture 
    if (typeof (window as any).ImageCapture !== "function") {
        return false;
    }

    return true;
}

export async function takePhoto(fileName: string) {
    // 1. 
    const stream = await navigator.mediaDevices.getUserMedia({
        video: { facingMode: "environment" }
    });

    const [track] = stream.getVideoTracks();
    const imageCapture = new ImageCapture(track);

    try {
        const blob = await imageCapture.takePhoto();
        const file = new File([blob], fileName, { type: blob.type });
        return file;

    } catch (e) {
        console.warn(":", e);

        return null;   // 
    } finally {
        // LED
        track.stop();
    }
}

export function checkFileShareable(file:File){
        return (navigator.canShare && navigator.canShare({ files: [file] }));
    }


let track: MediaStreamTrack | null = null;
let imageCapture: ImageCapture | null = null;

export async function startCameraPreview(videoElement: HTMLVideoElement) {
    const stream = await navigator.mediaDevices.getUserMedia({
        video: { facingMode: "environment" }
    });

    videoElement.srcObject = stream;
    await videoElement.play();

    track = stream.getVideoTracks()[0];
    imageCapture = new ImageCapture(track);
}

export async function capturePhoto(fileName: string) {
    if (!imageCapture) return null;

    try {
        const blob = await imageCapture.takePhoto();
        const file = new File([blob], fileName, { type: blob.type });
        return file;

    } catch (e) {
        console.warn(":", e);
        return null;

    } finally {
        track?.stop(); // 
    }
}