const Limits = {FLOAT32_MAX: 3.4028234663852886e+38};

export function isOverflow(val:number){
    return Limits.FLOAT32_MAX <= val;
}
export function isUnderflow(val:number){
    return val <= -Limits.FLOAT32_MAX;
}
export function isOverOrUnderflow(val:number){
    return Limits.FLOAT32_MAX <= Math.abs(val);
}