import { EventHandler } from "../evens-defs";
import { Button } from "./button";

const controlName = "file-select-button";

function createInputFileElement(){
    const input = document.createElement("input");
    input.type = "file";
    return input;
}

export class SelectFileButton extends Button {
    #inputFileElement;

    #selectedFile: File | null = null;
    get selectedFile() { return this.#selectedFile; }

    constructor(className?: string) {
        super(className);

        this.element.classList.add(controlName);
        if(className) this.element.classList.add(className);

        this.#inputFileElement = createInputFileElement();
        this.element.appendChild(this.#inputFileElement);

        this.element.addEventListener("click", (e:Event) => {
            this.#inputFileElement.click();
        });

        this.#inputFileElement.addEventListener("change", this.#onFileChanged);
    }

    

    #onFileChanged = (e:Event)=>{
        const file = this.#inputFileElement.files?.[0];
        if (file) {
            this.#selectedFile = file;
            this.onFileSelected?.(e);
        }
    }

    onFileSelected: EventHandler | null = null;
}