import { Control } from "./control";
import * as DeviceUtils from "../tools/device-utils";
import { CameraDevice } from "../tools/camera-device";

const controlName = "loading-view";

function createElement(){
    const view = document.createElement("div");
    view.classList.add(controlName);

    return view;
}

export class LoadingView extends Control {
    
    constructor(){
        const elem = createElement();
        super(elem);
    }

    open(parentElem:HTMLElement){
        parentElem.appendChild(this.element);
    }
    close(){
        this.element.parentElement?.removeChild(this.element);
    }
}