import { Control } from "./control";
import { CameraDevice } from "../tools/camera-device";
import { CSS_INVISIBLE } from "../core/constant";
import * as TimeUtils from "../tools/time-utils"

const controlName = "camera-view";

function createVideoElement() {
    const video = document.createElement("video");
    video.autoplay = true;
    video.playsInline = true; // iPhone
    video.muted = true;
    video.disablePictureInPicture = true;
    video.controls = false;
    return video;
}

function createCaptureButtonElement() {
    const button = document.createElement("button");
    button.classList.add("capture-button");
    return button;
}

function createCancelButtonElement() {
    const button = document.createElement("button");
    button.classList.add("cancel-button");
    return button;
}

function createElement(videoElem: HTMLVideoElement, captureButton: HTMLButtonElement, cancelButton: HTMLButtonElement) {
    const view = document.createElement("div");
    view.classList.add(controlName);

    view.appendChild(videoElem);
    view.appendChild(captureButton);
    view.appendChild(cancelButton);


    return view;
}

export class VideoView extends Control {
    #videoElement;
    #captureButtonElement;
    #cancelButtonElement;
    
    #camera;

    constructor() {
        const videoElem = createVideoElement();
        const captureButton = createCaptureButtonElement();
        const cancelButton = createCancelButtonElement();
        const elem = createElement(videoElem, captureButton, cancelButton);
        super(elem);

        this.#videoElement = videoElem;
        this.#captureButtonElement = captureButton;
        this.#cancelButtonElement = cancelButton;
        this.#camera = new CameraDevice(this.#videoElement);

        this.#captureButtonElement.addEventListener("click", this.#onCapture);
        this.#cancelButtonElement.addEventListener("click", this.#onCancel);
    }

    async show() {
        this.visible = true;
        await this.#camera.startCameraPreview();
    }
    hide() {
        this.#camera.stop();
        this.visible = false;
    }

    #onCapture = async () => {
        const timeStamp = TimeUtils.getTimestamp();
        const name = "lumimaplite-" + timeStamp + ".jpg";

        const file = await this.#camera.capturePhoto(name);
        // if(file){
        //     this.#resolve(file);
        // }
        // else{
        //     this.#reject();
        // }
        //this.#cleanup();

        if (file) {
            this.onCaptured?.(file);
        }
    }

    #onCancel = () => {
        this.#camera.stop();
        this.hide();
    }

    onCaptured?: (file: File) => void;
}