export const vertexShader = 
`
varying vec2 vUv;
void main() {
	vUv = uv;
	vec4 mvPosition = modelViewMatrix * vec4( position, 1.0 );
	gl_Position = projectionMatrix * mvPosition;
}
`;

export const builboradVertexShader = `
// markerVertexShader.glsl
uniform mat4 modelMatrix;
uniform mat4 viewMatrix;
uniform mat4 projectionMatrix;

uniform vec3 u_cameraPosition;
uniform float u_markerSize;     //  ()
uniform vec2 u_resolution;      // 

attribute vec3 position;        //  [-0.5, 0.5] 
attribute vec2 uv;

varying vec2 vUv;

void main() {
    vUv = uv;

    // 
    vec3 worldCenter = (modelMatrix * vec4(0.0, 0.0, 0.0, 1.0)).xyz;

    // 
    vec3 lookDir = normalize(u_cameraPosition - worldCenter);

    // 
    vec3 upDir = vec3(0.0, 1.0, 0.0);

    // 
    vec3 rightDir = normalize(cross(upDir, lookDir));
    upDir = normalize(cross(lookDir, rightDir)); // 

    // 
    float aspect = u_resolution.x / u_resolution.y;
    float pixelSize = u_markerSize / u_resolution.y; // Y

    // position.xy  [-0.5, 0.5] 
    vec3 billboardOffset = (rightDir * position.x * pixelSize * aspect) +
                           (upDir    * position.y * pixelSize);

    vec3 worldPos = worldCenter + billboardOffset;

    gl_Position = projectionMatrix * viewMatrix * vec4(worldPos, 1.0);
}
`

export const luminanceFragmentShader = 
`
varying vec2 vUv;
uniform sampler2D uTexLM;
uniform float uMin;
uniform float uMax;

float log10(float x){
	return 0.43429448190325176 * log(x);
}

void main() {
	float lm = float(texture2D(uTexLM, vUv));
	lm = lm == 0.0 ? 0.000001 : lm;
	float value = max(uMin, min(uMax, lm));
	float ratio = (log10(value)-log10(uMin))/(log10(uMax) - log10(uMin));
	float t = 1.0 - (ratio * 2.0);

	vec4 color = vec4(
		clamp(1.5 - abs(2.0 * t + 1.0), 0.15, 0.85),
		clamp(1.5 - abs(2.0 * t), 0.15, 0.85),
		clamp(1.5 - abs(2.0 * t - 1.0), 0.15, 0.85),
		1.0);

	gl_FragColor = color;
}
`;

export const luminanceRatioFragmentShader = 
`
varying vec2 vUv;
uniform sampler2D uTexRA;
uniform sampler2D uTexRAHOF;
uniform sampler2D uTexLM;
uniform float uBaseLm;
uniform float uMin;
uniform float uMax;

float log10(float x){
	return 0.43429448190325176 * log(x);
}

const vec4 RED = vec4(1.0, 0.0, 0.0, 1.0);
const vec4 YELLOW = vec4(1.0, 1.0, 0.0, 1.0);
const vec4 ORANGE = vec4(1.0, 0.5, 0.0, 1.0);
const vec4 LIGHTGREEN = vec4(0.0, 1.0, 0.0, 1.0);
const vec4 LIGHTBLUE = vec4(0, 1.0, 1.0, 1.0);
const vec4 BLUE = vec4(0.0, 0.0, 1.0, 1.0);
const vec4 WHITESMOKE = vec4(0.96,0.96,0.96,1.0);
const vec4 VIOLETLIKE = vec4(0.5, 0, 0.9, 1.0);

void main() {
	// vec4 raColor = vec4(texture2D(uTexRA, vUv).rgb, 0.8);
	// float of = float(texture2D(uTexRAHOF, vUv));
	float lm = float(texture2D(uTexLM, vUv));
	float value = lm/uBaseLm;

	vec4 color;
	if(value >= 10.0){
		color = RED;
	} 
	else if( value >= 3.0){
		color = ORANGE;
	}
	else if( value >= 2.0){
		color = YELLOW;
	}
	else if( value >= 0.5){
		color = WHITESMOKE;
	}
	else if( value >= 0.3333){
		color = LIGHTBLUE;
	}
	else if( value >= 0.1){
		color = BLUE;
	}
	else{
		color = VIOLETLIKE;
	}
	/*
	if(of == 2.0)
	{
		gl_FragColor = RED;
	}
	else if(of == 1.0)
	{
		gl_FragColor = VIOLETLIKE;
	}
	else
	{*/
		gl_FragColor = color;
	//}
}
`;

export const illuminanceFragmentShader = 
`
varying vec2 vUv;
uniform sampler2D uTexP;
uniform sampler2D uTexLM;
uniform float uMin;
uniform float uMax;
uniform float uRefRatio;

const float PI = 3.141592653589793;

float log10(float x){
	return 0.43429448190325176 * log(x);
}

const float FLOAT_MAX = 3.402823466e+38F;

void main() {
	vec4 raColor = vec4(texture2D(uTexP, vUv).rgb, 0.5);
	float lm = float(texture2D(uTexLM, vUv));
	if(lm <= -FLOAT_MAX || FLOAT_MAX <= lm){
		gl_FragColor = raColor;
	}
	else{
		float il = lm == 0.0 ? 0.000001 : ((lm * PI) / uRefRatio);
		il = max(uMin, min(uMax, il));
		float ratio = (log10(il)-log10(uMin))/(log10(uMax) - log10(uMin));
		float t = 1.0-(ratio * 2.0);

		vec4 color = vec4(
			clamp(1.5 - abs(2.0 * t + 1.0), 0.15, 0.85),
			clamp(1.5 - abs(2.0 * t), 0.15, 0.85),
			clamp(1.5 - abs(2.0 * t - 1.0), 0.15, 0.85),
			1.0);

		gl_FragColor = color;
		//gl_FragColor = vec4(vec3(t * 0.5 + 0.5), 1.0);
	}
}
`;

export const overflowFragmentShader = 
`
varying vec2 vUv;
uniform sampler2D uTexRA;
uniform sampler2D uTexRAOF;

const vec4 RED = vec4(1.0,0.0,0.0,1.0);

void main() {
	vec4 raColor = vec4(texture2D(uTexRA, vUv).rgb, 0.8);
	float value = float(texture2D(uTexRAOF, vUv));
	gl_FragColor = value == 1.0 ? RED : raColor;
}
`;

export const photometricOverflowFragmentShader = 
`
varying vec2 vUv;
uniform sampler2D uTexP;
uniform sampler2D uTexLM;

//const vec4 WHITE = vec4(1.0,1.0,1.0,1.0);
const vec4 RED = vec4(1.0,0.0,0.0,1.0);
const vec4 BLUE = vec4(0.0,0.0,1.0,1.0);
const vec4 BLACK = vec4(0.0,0.0,0.0,1.0);
const float FLOAT_MAX = 3.402823466e+38F;

void main() {
	vec4 raColor = vec4(texture2D(uTexP, vUv).rgb, 0.8);
	float value = float(texture2D(uTexLM, vUv));
	gl_FragColor = 
	   value >= FLOAT_MAX ? RED
	 : value <= -FLOAT_MAX ? BLUE 
	 : raColor;
}
`;