import { clamp } from "../../tools/math-utils";
import { EventHandler } from "../../evens-defs";
import { ScaleBar } from "./scalebar";
import * as ScaleBarUtil from "./scalebar-utility";

const munselNameValues = [
    {name:"--", value:Number.NaN},
    {name:"N10", value:1.0000},
    {name:"N9.5", value:0.8775},
    {name:"N9", value:0.7669},
    {name:"N8.5", value:0.6668},
    {name:"N8", value:0.5762},
    {name:"N7.5", value:0.4941},
    {name:"N7", value:0.4198},
    {name:"N6.5", value:0.3529},
    {name:"N6", value:0.293},
    {name:"N5.5", value:0.2397},
    {name:"N5", value:0.1927},
    {name:"N4.5", value:0.1519},
    {name:"N4", value:0.11700},
    {name:"N3.5", value:0.08778},
    {name:"N3", value:0.06391},
    {name:"N2.5", value:0.04498},
    {name:"N2", value:0.03048},
    {name:"N1.5", value:0.01971},
    {name:"N1", value:0.0118},
];

const MAX_LIMIT = 10000;
const MIN_LIMIT = 0.01;

export class IlluminanceScaleBar extends ScaleBar{
    #min = 1;
    #max = MAX_LIMIT;
    #ref = 0.279;
    #munselSelectElement;
    #minInputElement;
    #maxInputElement;
    #refInputElement;

    constructor(){
        super();
        
        this.element.classList.add("illuminance");
        this.title = "";
        
        const parts = this.#createConfigParts();
        this.configPanelElement.appendChild(parts.content);
        this.#munselSelectElement = parts.selectMunsel;
        this.#minInputElement = parts.minInpput;
        this.#maxInputElement = parts.maxInput;
        this.#refInputElement = parts.refInput;

        this.#minInputElement.addEventListener("change", (e)=>{
            this.#min = parseFloat(this.#minInputElement.value);
            if(this.#min <= 0) this.#min = MIN_LIMIT;
            this.drawScale();
            this.onMinChanged?.(e);
        });
        this.#maxInputElement.addEventListener("change", (e)=>{
            this.#max = parseFloat(this.#maxInputElement.value);
            if(this.#max <= 0) this.#max = MAX_LIMIT;
            this.drawScale();
            this.onMaxChanged?.(e);
        });

        this.#munselSelectElement.addEventListener("change", (e)=>{
            const index = this.#munselSelectElement.selectedIndex;
            if(index != 0){
                const val = munselNameValues[index].value;
                this.#refInputElement.value = String(val);
                this.#ref = val;
                this.onReflectionChanged?.(e);
            }
        });

        this.#refInputElement.addEventListener("change", (e)=>{
            const value = parseFloat(this.#refInputElement.value);
            if(value){
                this.#ref = value;
                this.#updateSelection();
                this.onReflectionChanged?.(e);
            }
        })

        this.drawScale();
    }

    #createConfigParts(){
        const frag = document.createDocumentFragment();

        const line1 = document.createElement("p");
        const title1 = document.createElement("span");
        title1.classList.add("title");
        title1.textContent = "";
        line1.appendChild(title1);

        const min = document.createElement("input");
        min.type = "number";
        min.step = String(1);
        min.max = String(MAX_LIMIT);
        min.min = String(MIN_LIMIT);
        min.pattern = "/*d/";

        const max = document.createElement("input");
        max.type = "number";
        max.step = String(1);
        max.max = String(MAX_LIMIT);
        max.min = String(MIN_LIMIT);
        max.pattern = "/*d/";

        line1.appendChild(min);
        line1.appendChild(document.createTextNode(" - "));
        line1.appendChild(max);
        frag.appendChild(line1);

        const line2 = document.createElement("p");
        const title2 = document.createElement("span");
        title2.classList.add("title");
        title2.textContent = "";
        
        line2.appendChild(title2);

        const munselSelect = document.createElement("select");
        for(let i=0;i<munselNameValues.length;i++){
            const option = document.createElement("option");
            option.value = munselNameValues[i].name;
            option.innerHTML = munselNameValues[i].name;
            munselSelect.appendChild(option);
        }
        line2.appendChild(munselSelect);

        const refInput = document.createElement("input");
        refInput.type = "number";
        refInput.step = String(0.01);
        refInput.min = String(0.000);
        refInput.max = String(1.000);
        refInput.value = String(1.000);
        refInput.pattern = "[0-9][.][0-9]{4}";
        line2.appendChild(refInput);
        frag.appendChild(line2);

        return {
            content:frag,
            minInpput:min,
            maxInput:max,
            refInput:refInput,

            selectMunsel:munselSelect,
        };
    }

    get min (){return this.#min;}
    set min(value:number){        
        this.#min = clamp(value, MIN_LIMIT, MAX_LIMIT);
        this.#minInputElement.value = String(this.#min);
        this.drawScale();
    }
    get max (){return this.#max;}
    set max(value:number){        
        this.#max = clamp(value, MIN_LIMIT, MAX_LIMIT);
        this.#maxInputElement.value = String(this.#max);
        this.drawScale();
    }

    get reflection (){return this.#ref;}
    set reflection(value:number){
        this.#ref = value;
        this.#updateSelection();
    }

    #updateSelection(){
        let index = -1;
        const val = this.#ref;
        for(let i=0;i<munselNameValues.length;i++){
            if(val == munselNameValues[i].value){
                index = i;
                break;
            }
        }
        
        if(index == -1) return;

        this.#munselSelectElement.selectedIndex = index;
    }

    #emitOnReflectionChanged(){
    
    }

    drawScale(){
        ScaleBarUtil.drawScale(this.canvasElement, this.#min, this.#max);
    }

    onMinChanged:EventHandler | null = null;
    onMaxChanged:EventHandler | null = null;
    onReflectionChanged:EventHandler | null = null;
}