import { Control } from "./control";
import { CameraDevice } from "../tools/camera-device";
import { CSS_INVISIBLE } from "../core/constant";
import * as TimeUtils from "../tools/time-utils"

const controlName = "image-view";


function createImgElement() {
    const img = document.createElement("img");
    

    return img;
}

function createElement(img: HTMLImageElement) {
    const view = document.createElement("div");
    view.classList.add(controlName);

    view.appendChild(img);


    return view;
}

export class ImageView extends Control {

    #imgElement;

    #objectURL?:string;

    constructor() {
        const img = createImgElement();
        const elem = createElement(img);
        super(elem);

        this.#imgElement = img;
    }
    showImage(jpg: File) {
        if(this.#objectURL){
            URL.revokeObjectURL(this.#objectURL);
            this.#objectURL = undefined;
        }

        this.#objectURL = URL.createObjectURL(jpg);
        this.#imgElement.src = this.#objectURL;
        // const reader = new FileReader();
        // reader.onload = (event) => {
        //     const img = new Image();
        //     img.onload = () => {
        //         // canvas
        //         this.#imgElement.width = img.width; // canvas
        //         this.#imgElement.height = img.height; // canvas
        //         const ctx = this.#imgElement.getContext("2d");
        //         ctx?.drawImage(img, 0, 0);
        //     }
        //     img.src = event.target?.result as string;
        // }
        // reader.readAsDataURL(jpg);
    }
}