import { clamp } from "../../tools/math-utils";
import { ImageTypes } from "../../core/type-defs";
import { EventHandler } from "../../evens-defs";
import { ScaleBar } from "./scalebar";
import * as ScaleBarUtil from "./scalebar-utility";

const MAX_LIMIT = 100000;
const MIN_LIMIT = 0.0001;
const DISPLAY_MIN_LIMIT = 0;
export class LuminanceScalerBar extends ScaleBar{
    #min = 1;
    #max = 10000;

    #minInputElement;
    #maxInputElement;

    static SCALEBAR_WIDTH = 20;
    constructor(){
        super();
        
        this.element.classList.add("luminance");
        this.title = "";

        const configParts = this.#createConfigContentParts();

        this.configPanelElement.appendChild(configParts.content);
        this.#minInputElement = configParts.minInput;
        this.#maxInputElement = configParts.maxInput;
        
        this.#minInputElement.addEventListener("change",(e:Event)=>{
            this.#min = parseFloat(this.#minInputElement.value);
            if(this.#min <= 0) this.#min = MIN_LIMIT;
            this.drawScale();
            this.onMinChanged?.(e);
        });
        
        this.#maxInputElement.addEventListener("change",(e:Event)=>{
            this.#max = parseFloat(this.#maxInputElement.value);
            if(this.#max <= 0) this.#max = MIN_LIMIT;
            this.drawScale();
            this.onMaxChanged?.(e);
        });
        
        this.drawScale();
    }

    #createConfigContentParts (){
        const frag = document.createDocumentFragment();
        
        const line1 = document.createElement("p");
        const title1 = document.createElement("span");
        title1.classList.add("title");
        title1.textContent = "";
        line1.appendChild(title1);

        const min = document.createElement("input");
        min.type = "number";
        min.max = String(MAX_LIMIT);
        min.min = String(DISPLAY_MIN_LIMIT);
        min.step = String(1);
        min.pattern = "/[0-9]*/";
        
        const max = document.createElement("input");
        max.type = "number";
        max.max = String(MAX_LIMIT);
        max.min = String(DISPLAY_MIN_LIMIT);
        max.step = String(1);
        max.pattern = "/[0-9]*/";

        line1.appendChild(min);
        line1.appendChild(document.createTextNode(" - "));
        line1.appendChild(max);

        frag.appendChild(line1);
        return {
            content:frag,
            minInput:min,
            maxInput:max
        };
    }

    drawScale(){
        ScaleBarUtil.drawScale(this.canvasElement, this.#min, this.#max);
    }   

    get min(){return this.#min;}
    set min(min:number){
        this.#min = clamp(min, MIN_LIMIT, MAX_LIMIT);
        this.#minInputElement.value = String(this.#min);
        this.drawScale();
    }

    get max(){return this.#max;}
    set max(max:number){
        this.#max = clamp(max, MIN_LIMIT, MAX_LIMIT);
        this.#maxInputElement.value = String(this.#max);
        this.drawScale();
    }

    onMinChanged:EventHandler | null = null;
    onMaxChanged:EventHandler | null = null;
}