import { ImageTypes } from "../core/type-defs";
import { IlluminanceScaleBar } from "./scalebars/illuminance-scalebar";
import { LuminanceScalerBar } from "./scalebars/luminance-scalebar";
import { LuminanceRatioScalerBar } from "./scalebars/luminanceratio-scalebar";
import { PhotometoricOverflowScaleBar } from "./scalebars/overflow-scalebar";
import { ScaleBar } from "./scalebars/scalebar";

const controlName = "scalebarcontainer";
export class ScaleBarContainer{
    #element;
    #luminanceScaleBar = new LuminanceScalerBar();
    #luminanceRatioScaleBar = new LuminanceRatioScalerBar();
    #illuminanceScaleBar = new IlluminanceScaleBar();
    #photometricOverflowScaleBar = new PhotometoricOverflowScaleBar();
    get luminanceScaleBar(){return this.#luminanceScaleBar;}
    get luminanceRatioScaleBar(){return this.#luminanceRatioScaleBar;}
    get illuminanceScaleBar(){return this.#illuminanceScaleBar;}
    get photometricOverflowScaleBar(){return this.#photometricOverflowScaleBar;}

    #visible = true;

    #currentScaleBar:ScaleBar|null = null;
    #currentType:ImageTypes = "original";

    constructor(){
        this.#element =  this.#createElement();
        
    }   

    #createElement(){
        const container = document.createElement("div");
        container.classList.add(controlName);
        
        return container;
    }

    
    showScalebar(imageType:ImageTypes){
        this.removeScaleBar();

        switch(imageType){
            case "original":
                break;
            case "luminance":
                this.#currentScaleBar = this.#luminanceScaleBar;
                break;
            case "illuminance":
                this.#currentScaleBar = this.#illuminanceScaleBar;
                break;
            case "luminanceRatio":
                this.#currentScaleBar = this.#luminanceRatioScaleBar;
                break;
            case "photometricOverflow":
                this.#currentScaleBar = null;
                break;
        }

        this.#currentType = imageType;

        if(this.#currentScaleBar){
            this.#element.appendChild(this.#currentScaleBar.element);
            this.#currentType = imageType;
        }
    }

    removeScaleBar(){
        if(this.#currentScaleBar) {
            this.#element.removeChild(this.#currentScaleBar.element);
            this.#currentScaleBar = null;
        }
    }

    get visible(){return this.#visible;}
    set visible(visible:boolean){
        this.#visible = visible;

        this.#element.style.display = visible ? "" : "none";
    }
    get element(){return this.#element;}
}