import { EventHandler } from "../evens-defs";
import { Button } from "./button";
import { Control } from "./control";

const controlName = "illuminance-control-bar"

function createElement(className?:string){
    const controlbar = document.createElement("ul");
    controlbar.classList.add(controlName);
    if(className) controlbar.classList.add(className);
    return controlbar;
}

export class IlluminanceControlBar extends Control {    
    #measureWhiteButton;
    #measureGrayscaleButton;
    #measureIlluminanceSensorButton;

    constructor(className?:string){
        const element = createElement(className);
        super(element);
        this.#measureWhiteButton = new Button("measure-white-button");
        this.#measureGrayscaleButton = new Button("measure-grayscale-button");
        this.#measureIlluminanceSensorButton = new Button("measure-illuminance-sensor-button");

        this.#measureWhiteButton.onClick = this.#onMeasureWhiteButtonClick;
        this.#measureGrayscaleButton.onClick = this.#oMeasureGrayscaleButtonClick;
        this.#measureIlluminanceSensorButton.onClick = this.#onMeasureIlluminanceSensorButtonClick;

        this.addItem(this.#measureWhiteButton.element);
        this.addItem(this.#measureGrayscaleButton.element);
        this.addItem(this.#measureIlluminanceSensorButton.element);

        
    }

    addItem(elem:HTMLElement){
        const item = document.createElement("li");
        item.classList.add("item");
        item.appendChild(elem);
        this.element.appendChild(item);
    }

    #onMeasureWhiteButtonClick = (e:Event)=>{
        this.onMeasureWhiteButtonClick?.(e);
    }
    #oMeasureGrayscaleButtonClick = (e:Event) => {
        this.onMeasureGrayscaleButtonClick?.(e);
    }
    #onMeasureIlluminanceSensorButtonClick = (e:Event) =>{
        this.onMeasureIlluminanceSensorButtonClick?.(e);
    }

    onMeasureWhiteButtonClick:EventHandler|null = null;
    onMeasureGrayscaleButtonClick:EventHandler|null = null;
    onMeasureIlluminanceSensorButtonClick:EventHandler|null = null;
}