import { ImageTypes, imageNames } from "../core/type-defs";
import { EventHandler } from "../evens-defs";


const controlName = "imageselector";

export class ImageSelector 
{
    #element;
    #items:ImageTypes[] = [];
    #visible = true;

    #selectionChanedHandler:EventHandler | null = null;

    constructor(){
        this.#element = createElement();
        this.#reloadOptionElement(this.#items);

        this.#attachEvents();

        function createElement()
        {
            const select = document.createElement("select");
            select.classList.add(controlName);

            return select;
        }
    }

    #attachEvents(){
        this.#element.addEventListener("change", (e:Event)=>{
            if(this.#selectionChanedHandler) {
                this.#selectionChanedHandler(e);
            }
        })
    }

    get items(){return this.#items;}
    set items(imageTypes:ImageTypes[]){
        this.#items = imageTypes;
        this.#reloadOptionElement(imageTypes);
    }

    #reloadOptionElement(imageTypes:ImageTypes[]){
        const frag = document.createDocumentFragment();
        for(let i=0;i<imageTypes.length;i++)
        {
            const option = document.createElement("option");
            option.textContent = imageNames.get(imageTypes[i])!;

            frag.appendChild(option);
        }

        this.#element.innerHTML = "";
        this.#element.appendChild(frag);
    }

    get visible(){return this.#visible;}
    set visible(tf:boolean){
        this.#visible = tf;
        this.#element.style.display = tf ? "" : "none";
    }

    get element(){return this.#element as HTMLElement;}

    get selectedImageType() { return this.#items[this.#element.selectedIndex];}
    set selectedImageType(type:ImageTypes) {
        this.#element.selectedIndex = this.#items.indexOf(type);
    }

    get onSelectionChanged(){return this.#selectionChanedHandler;}
    set onSelectionChanged(handler:EventHandler | null){
        this.#selectionChanedHandler = handler;
    }
}