export class Point{
    x:number = 0;
    y:number = 0;

    constructor(x?:number, y?:number){
        if(x) this.x = x;
        if(y) this.y = y;
    }
}

export class Size{
    width:number = 0;
    height:number = 0;

    constructor(width=0, height=0){
        this.width = width;
        this.height = height;
    }
    aspect(){return this.width/ this.height;}

    get isEmpty(){return this.width === 0 && this.height === 0;}
}

export class Rectangle{
    x:number = 0;
    y:number = 0;
    width:number = 0;
    height:number = 0;

    constructor(x?:number, y?:number, width?:number, height?:number){
        if(x) this.x = x;
        if(y) this.y = y;
        if(width) this.width = width;
        if(height) this.height = height;
    }
    get location(){return new Point(this.x,this.y)}
    set location(point:Point){this.x = point.x, this.y = point.y;}
    get size(){return new Size(this.width, this.height);}
    set size(size:Size){this.width=size.width; this.height = size.height;}

    get left(){return Math.min(this.x, this.x+this.width);}
    get right(){return Math.max(this.x, this.x+this.width);}
    get top(){return Math.min(this.y, this.y+this.height);}
    get bottom(){return Math.max(this.y, this.y+this.height);}

}
