import Compressor from 'compressorjs';
import * as FileUtils from "../tools/file-utils"
import { Exif } from './exif';
import Piexif from 'piexifjs';

export class CameraDevice {
    #videoElement;

    track: MediaStreamTrack | null = null;
    imageCapture: ImageCapture | null = null;

    constructor(videoElement: HTMLVideoElement) {
        this.#videoElement = videoElement;
    }

    async checkCameraAvailable() {
        // 1. ImageCapture 
        if (typeof (window as any).ImageCapture !== "function") {
            return false;
        }

        // 2. 
        try {
            const stream = await navigator.mediaDevices.getUserMedia({
                video: true
            });

            const track = stream.getVideoTracks()[0];
            if (!track) {
                return false;
            }

            // 3.  track  ImageCapture 
            try {
                const ic = new (window as any).ImageCapture(track);

                // takePhoto 
                const ok = typeof ic.takePhoto === "function";

                // 
                track.stop();

                return ok;
            } catch {
                track.stop();
                return false;
            }
        } catch {
            // 
            return false;
        }
    }

    async startCameraPreview() {
        const stream = await navigator.mediaDevices.getUserMedia({
            video: { facingMode: "environment" }
        });

        this.#videoElement.srcObject = stream;
        await this.#videoElement.play();

        this.track = stream.getVideoTracks()[0];
        this.imageCapture = new ImageCapture(this.track);
    }

    async capturePhoto(fileName: string) {
        if (!this.imageCapture) return null;

        try {
            //const angle = window.screen.orientation.angle;
            //alert(angle);
            const blob = await this.imageCapture.takePhoto();
            let file = new File([blob], fileName, { type: blob.type });
            
            // const exif = await Exif.load(file);
            // // 
            // if (exif.orientation == 1 && angle !== 0) {
            //     file = await remakeFile(file, exif, angle);
            // }

            return file;

        } catch (e) {
            console.warn(":", e);
            return null;

        } finally {
            //this.track?.stop(); // 
        }
    }

    stop(){
        this.track?.stop();
    }
}

// 
async function compressImage(file: File): Promise<File> {
    return new Promise((resolve, reject) => {
        // (bytes) JPEG 
        const maxSizeInBytes = 800 * 1024; // =0.8MB in bytes

        // 
        new Compressor(file, {
            quality: 0.8,
            checkOrientation: true, // Exif
            retainExif: true,       // ExifBlob
            success(result: File) {
                resolve(result);
            },
            error(err) {
                reject(err);
            },
        });
    });
}

// 
/**
 * Data URLBlob/File
 * @param dataurl ExifData URL
 * @param type MIME
 * @returns Blob
 */
function dataURLToBlob(dataurl: string, type: string): Blob {
    const parts = dataurl.split(';base64,');
    if (parts.length < 2) {
        throw new Error('Invalid Data URL format');
    }
    const bstr = atob(parts[1]);
    let n = bstr.length;
    const u8arr = new Uint8Array(n);
    while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
    }
    return new Blob([u8arr], { type: type });
}

/**
 * FileExif
 * * @param file 
 * @param exif Exifpiexif.load()
 * @param angle 90, 180, 270
 * @returns ExifFile
 */
async function remakeFile(file: File, exif:Exif, angle:number): Promise<File> {
    if (file.type !== 'image/jpeg') {
        throw new Error("Only JPEG files are supported for Exif insertion.");
    }
    
    // 1. Data URL
    const arrayBuffer = await file.arrayBuffer();
    const originalDataUrl = arrayBufferToDataURL(arrayBuffer, file.type);
    
    const img = new Image();
    const objectUrl = URL.createObjectURL(file);
    
    // Promise
    await new Promise<void>((resolve, reject) => {
        img.onload = () => {
            URL.revokeObjectURL(objectUrl);
            resolve();
        };
        img.onerror = reject;
        img.src = objectUrl;
    });

    // 2. Canvas
    const canvas = document.createElement('canvas');
    const ctx = canvas.getContext('2d')!;
    
    // Canvas
    const width = img.naturalWidth;
    const height = img.naturalHeight;
    
    let rotatedWidth = width;
    let rotatedHeight = height;

    if (angle === 90 || angle === 270) {
        // 90270
        rotatedWidth = height;
        rotatedHeight = width;
    }
    
    canvas.width = rotatedWidth;
    canvas.height = rotatedHeight;

    // 3. 
    ctx.save();
    
    switch (angle) {
        case 90: // 90
            ctx.translate(rotatedWidth, 0);
            ctx.rotate(Math.PI / 2);
            break;
        case 180: // 180
            ctx.translate(rotatedWidth, rotatedHeight);
            ctx.rotate(Math.PI);
            break;
        case 270: // 90
            ctx.translate(0, rotatedHeight);
            ctx.rotate(Math.PI * 1.5); //  -Math.PI / 2
            break;
        default: // 0 (90, 180, 270)
            ctx.translate(0, 0);
            break;
    }

    // 4. 
    // 
    ctx.drawImage(img, 0, 0, width, height);
    ctx.restore();

    // 5. JPEGExif
    const newJpegWithoutExif = canvas.toDataURL('image/jpeg', 0.95); // 0.95
    
    // 6. Exif
    // ExifOrientation1
    exif.orientation = 1;

    // 7. Exif
    const finalJpegDataUrl = exif.insert(newJpegWithoutExif);
    
    // 8. Data URLFile
    const finalBlob = dataURLToBlob(finalJpegDataUrl, file.type);
    
    // 
    return new File([finalBlob], file.name, { type: file.type, lastModified: Date.now() });
}

//  :  piexifjs 
// ()

/**
 * ArrayBuffer  Data URL piexifjs
 */
function arrayBufferToDataURL(buffer: ArrayBuffer, type: string): string {
    const binary = new Uint8Array(buffer);
    let data = '';
    for (let i = 0; i < binary.length; i++) {
        data += String.fromCharCode(binary[i]);
    }
    return 'data:' + type + ';base64,' + btoa(data);
}
function getOrientation() {
    if (window.matchMedia("(orientation: portrait)").matches) {
        return "Portrait";
    } else {
        return "Landscape";
    }
}
