import { Point } from "../core/common";

type MarkerTypes = "normal" | "baseLuminance";

const controlName = "marker";

export class Marker{
    #element;
    #tipsElement;

    #location = new Point();
    #imageCoordinates = new Point();

    #text = "";

    #tipsVisible = true;
    #visible = true;

    name:string = "";
    #type:MarkerTypes = "normal";
    
    constructor(){
        const parts = this.#createElementParts();
        this.#element = parts.marker;
        this.#tipsElement = parts.tips;
    }

    #createElementParts(){
        const marker = document.createElement("div");
        marker.classList.add(controlName);

        const finder = document.createElement("div");
        finder.classList.add("finder");

        marker.appendChild(finder);

        const tips = document.createElement("div");
        tips.classList.add("tips");
        marker.appendChild(tips);

        return {
            marker:marker,
            tips:tips
        };
    }

    #setLocation(x:number, y:number){
        this.#element.style.left = x + "px";
        this.#element.style.top = y + "px";
    }

    get element(){return this.#element;}

    get location(){return this.#location;}
    set location(location:Point){
        this.#location = location;
        this.#setLocation(location.x, location.y);
    }

    get imagePoint(){return this.#imageCoordinates;}
    set imagePoint(imageCoord:Point){this.#imageCoordinates = imageCoord;}

    get tipsVisible(){return this.#tipsVisible;}
    set tipsVisible(tf:boolean)
    {
        this.#tipsVisible = tf;
        this.#tipsElement.style.display = tf ? "" : "none";
    }

    setTipsContent(elem:HTMLElement|DocumentFragment|undefined){
        if(elem){
            this.#tipsElement.innerHTML = "";
            this.#tipsElement.appendChild(elem);
            this.tipsVisible = true;
        }
        else{
            this.tipsVisible = false;
        }
    }

    get visible(){return this.#visible;}
    set visible(visible:boolean){
        if(visible == this.#visible) return;
        this.#visible= visible;
        this.element.style.display = visible ? "" : "none";
    }

    get type(){return this.#type;}
    set type(type:MarkerTypes){
        this.#type = type;
        switch(type){
            case "baseLuminance": 
                this.#element.classList.add("circle");
                break;
            case "normal":
            default:
                this.#element.classList.remove("circle");
                break;
        }
    }
}