import { DialogBase } from "./dialog-base";

function createMessageElement(){
    const p = document.createElement("p");
    p.classList.add("message");
    return p;
}

function createFormContent(messageElement:HTMLParagraphElement) {
    const form = document.createElement("form");
    form.method = "dialog";

    form.appendChild(messageElement);

    const answer = document.createElement("div");
    answer.classList.add("answer");

    const yes = document.createElement("button");
    yes.classList.add("yes");
    yes.value = "yes";
    yes.textContent = "";
    answer.appendChild(yes);

    const no = document.createElement("button");
    no.value = "no";
    no.classList.add("no");
    no.textContent = "";
    answer.appendChild(no);

    form.appendChild(answer);
    return form;
}

export class ConfirmDialog extends DialogBase {
    #messageElement;
    #message = "";

    constructor(className?:string) {
        const textElem = createMessageElement();
        const form = createFormContent(textElem);
        super(form);

        this.#messageElement = textElem;
    }

    get message(){return this.#message;}
    set message(text:string){
        this.#message = text;
        this.#messageElement.textContent = text;
    }
}