import * as piexif from "piexifjs"

export function getExifInfo(file: File): Promise<any> {
    return new Promise((resolve, reject) => {
        const reader = new FileReader();

        reader.onload = function (e) {
            try {
                const dataUrl = e.target!.result as string;
                const exif = piexif.load(dataUrl);
                const converted = convertExif(exif)
                resolve(converted);
            } catch (err) {
                reject(err);
            }
        };

        reader.onerror = () => reject(reader.error);

        reader.readAsDataURL(file);
    });
}

function convertExif(data:any) {
    const maps:any = {
        "0th": piexif.ImageIFD,
        "Exif": piexif.ExifIFD,
        "GPS": piexif.GPSIFD,
        "Interop": piexif.InteropIFD,
        "1st": piexif.ImageIFD,
    };

    const result:any = {};

    for (const ifdKey in data) {
        if (!maps[ifdKey]) continue;
        const values:any = {};

        const idMap:Record<string, string> = maps[ifdKey];

        const ifd:Record<string, any> = data[ifdKey];
        for (const id in ifd) {
            let name = id;
            for(const key in idMap){
                if(idMap[key] == id){
                    name = key;
                    break;
                }
            }
            values[name] = ifd[id];
        }
        result[ifdKey] = values;
    }
    

    return result;
}

