
import { DialogBase } from "./dialog-base";

const controlName = "upload-file-dialog";

function createInputTitleElement() {
    const input = document.createElement("input");
    input.type = "text";
    input.classList.add("title");

    return input;
}

function createInputMessageElement() {
    const textarea = document.createElement("textarea");
    textarea.classList.add("message");

    return textarea;
}

function createFormElement(titleElement: HTMLInputElement, messageElement: HTMLTextAreaElement) {

    const form = document.createElement("form");
    form.classList.add("content");

    const titleP = document.createElement("p");
    titleP.textContent = "";

    form.appendChild(titleP);
    form.appendChild(titleElement);
    const messageP = document.createElement("p");
    messageP.textContent = "";
    form.appendChild(messageP);
    form.appendChild(messageElement);

    const answer = document.createElement("answer");
    answer.classList.add("answer");

    const ok = document.createElement("button");
    ok.classList.add("ok");
    ok.value = "ok";
    ok.textContent = "OK";
    answer.appendChild(ok);

    const cancel = document.createElement("button");
    cancel.classList.add("cancel");
    cancel.value = "cancel";
    cancel.textContent = "";
    answer.appendChild(cancel);

    form.appendChild(answer);

    return form;
}

export class UploadFileDialog extends DialogBase {
    #formElement;
    #titleElement;
    #messageElement;

    constructor() {
        const titleElement = createInputTitleElement();
        const messageElement = createInputMessageElement();
        const form = createFormElement(titleElement, messageElement);

        super(form);

        this.#formElement = form;
        this.#titleElement = titleElement;
        this.#messageElement = messageElement;
        this.element.classList.add(controlName);

        this.#formElement.addEventListener("submit", this.#onSubmit)
    }

    get message() { return this.#messageElement.value; }
    set message(text: string) { this.#messageElement.value = text; }

    get titleText() { return this.#titleElement.value; }
    set titleText(text: string) { this.#titleElement.value = text; }

    #onSubmit = (e: SubmitEvent) => {
        e.preventDefault();

        // 
        this.element.returnValue =(e.submitter as HTMLButtonElement).value;
        switch (this.element.returnValue) {
            case "ok":
                const titleText = this.titleText.trim()
                if (titleText === "") {
                    alert("");            
                    return;
                }

                const messageText = this.message.trim()
                if (messageText === "") {
                    alert("");
                    return;
                }
                break;

            case "cancel":
            default:
                break;
        }

        this.element.close(this.element.returnValue);
    }
}