import { EventHandler } from "../evens-defs";
import { Control } from "./control";

const checkedClassName = "--checked";
const controlName = "toggle-button"

function createElement(className?:string){
    const checkbox = document.createElement("div");
    checkbox.classList.add(controlName);
    if(className) checkbox.classList.add(className);

    return checkbox;
}

export class ToggleButton extends Control {
    
    #checked = false;

    constructor(className?:string){
        const element = createElement(className);
        element.classList.add(controlName);
        super(element);
        
        
        element.addEventListener("click", this.#onClick);
    }

    #onClick = (e:Event)=>{
        this.#checked = !this.#checked;
        this.onCheckedChanged?.(e);
    }


    get checked(){return this.#checked;}
    set checked(checked:boolean){ 
        if(checked == this.#checked) return;
        this.#checked = checked;
        if(this.#checked){
            this.element.classList.add(checkedClassName);
        }else{
            this.element.classList.remove(checkedClassName);
        }
    }

    onCheckedChanged:EventHandler | null = null;
}