import { ImagingParameter } from "./settings";
import { ImageTypes } from "./type-defs";
import * as THREE from "three";
import * as shader from "./shader";
import { LumimapImageData } from "./lumimapimagedata";

export class ResourceManager {
    #width: number = 0;
    #height: number = 0;
    #imageDataMap = new Map<ImageTypes, LumimapImageData>();
    #materialMap = new Map<ImageTypes, THREE.Material>();
    #textureMap = new Map<ImageTypes, THREE.Texture>();

    get images() { return this.#imageDataMap; }
    get materials() { return this.#materialMap; }
    get imageWidth() { return this.#width; }
    get imageHeight() { return this.#height; }

    async loadAsync(jpeg: File, luminanceData: Float32Array, width: number, height: number) {
        this.clear();

        this.#width = width;
        this.#height = height;

        this.#imageDataMap.set("luminance", new LumimapImageData(luminanceData, width, height));

        const luminanceTexture = this.createDataTexture(luminanceData, width, height);
        const originalTexture = await this.createTextureFromFile(jpeg);
        originalTexture.colorSpace = THREE.SRGBColorSpace;

        this.#textureMap.set("original", originalTexture);
        this.#textureMap.set("luminance", luminanceTexture);

        this.#materialMap.set("original", this.createBasicMaterial(originalTexture));
        this.#materialMap.set("luminance", this.createLuminanceMaterial(luminanceTexture));
        this.#materialMap.set("illuminance", this.createIlluminanceMaterial(originalTexture, luminanceTexture));

        this.#materialMap.set("luminanceRatio", this.createLuminanceRatioMaterial(luminanceTexture));
        this.#materialMap.set("photometricOverflow", this.createPhotometricOverflowMaterial(originalTexture, luminanceTexture));
    }

    clear() {
        this.#imageDataMap.clear();

        for (const item of this.#materialMap) {
            item[1].dispose();
        }
        this.#materialMap.clear();

        for (const item of this.#textureMap) {
            item[1].dispose();
        }
        this.#textureMap.clear();
    }

    async createTextureFromFile(file: File): Promise<THREE.Texture> {
        return new Promise((resolve, reject) => {
            const url = URL.createObjectURL(file);
            const loader = new THREE.TextureLoader();
            loader.load(
                url,
                texture => {
                    URL.revokeObjectURL(url); // 
                    resolve(texture);
                },
                undefined,
                err => reject(err)
            );
        });
    }

    createDataTexture(float32ary: Float32Array, width: number, height: number) {
        let texture = new THREE.DataTexture(float32ary, width, height, THREE.RedFormat, THREE.FloatType);
        texture.needsUpdate = true;//
        texture.flipY = true;
        return texture;
    }

    createBasicMaterial(texture: THREE.Texture) {
        let mat = new THREE.MeshBasicMaterial({ name: "original", map: texture });
        return mat;
    }

    createLuminanceMaterial(lmTex: THREE.Texture, min: number = 0.0001, max: number = 10000) {
        let mat = new THREE.ShaderMaterial({
            name: "luminance",
            uniforms: {
                uTexLM: { value: lmTex },
                uMin: { value: min },
                uMax: { value: max },
            },
            vertexShader: shader.vertexShader,
            fragmentShader: shader.luminanceFragmentShader
        });
        return mat;
    }

    createLuminanceRatioMaterial(lmTex: THREE.Texture, min: number = 0, max: number = 30, baseLm: number = 200) {
        let mat = new THREE.ShaderMaterial({
            name: "luminanceRatio",
            uniforms: {
                uTexLM: { value: lmTex },
                uMin: { value: min },
                uMax: { value: max },
                uBaseLm: { value: baseLm },
            },
            vertexShader: shader.vertexShader,
            fragmentShader: shader.luminanceRatioFragmentShader
        });
        return mat;
    }

    createIlluminanceMaterial(photoTex: THREE.Texture, lmTex: THREE.Texture, min: number = 1, max: number = 3000, refRatio: number = 0.5) {
        const mat = new THREE.ShaderMaterial({
            name: "illuminance",
            uniforms: {
                uTexP: { value: photoTex },
                uTexLM: { value: lmTex },
                uMin: { value: min },
                uMax: { value: max },
                uRefRatio: { value: refRatio },
            },
            vertexShader: shader.vertexShader,
            fragmentShader: shader.illuminanceFragmentShader
        });
        return mat;
    }

    createPhotometricOverflowMaterial(photoTex: THREE.Texture, luminanceTex: THREE.Texture) {
        const mat = new THREE.ShaderMaterial({
            name: "photometricOverflow",            
            uniforms: {
                uTexP: { value: photoTex },
                uTexLM: { value: luminanceTex }
            },
            transparent: true,
            vertexShader: shader.vertexShader,
            fragmentShader: shader.photometricOverflowFragmentShader

        });
        return mat;
    }

}