import { LumimapLite } from "./core/lumimaplite";

if ('serviceWorker' in navigator) {
    navigator.serviceWorker.register('./service-worker.js');
}

document.addEventListener("DOMContentLoaded", async function(){
    const lumimapLiteElement = document.querySelector(".lumimaplite") as HTMLDivElement;
    if(lumimapLiteElement){
        const lumimapLite = new LumimapLite(lumimapLiteElement);
        await lumimapLite.init();
    }
});