import { ImageTypes } from "../../core/type-defs";
import * as ScaleBarUtil from "./scalebar-utility";

const CANVAS_WIDTH = 80;
const CANVAS_HEIGHT = 120;
const SCALEBAR_WIDTH = 20;

const verticalMargin = 5; // 
const PIXEL_OFFSET = 0.5;

const controlName = "scalebar";

export class ScaleBar{
    #element;
    #titleElement;
    #canvasElement;
    #configButtonElement;
    #configPanelElement;
    
    #title = "";
    get title (){return this.#title;};
    set title (title:string){
        this.#title = title;
        this.#titleElement.textContent = title;
    }

    figureType:ImageTypes = "luminance";

    #isConfigPanelOpen = false;
    #visible = true;
    #configable = true;
    
    constructor(){
        const parts = this.#createElementParts();

        this.#element = parts.scalebar;
        this.#titleElement = parts.title;
        this.#canvasElement = parts.drawArea;
        this.#canvasElement.width = CANVAS_WIDTH;
        this.#canvasElement.height = CANVAS_HEIGHT;
        this.#configButtonElement = parts.configButton;
        this.#configPanelElement = parts.configPanel;

        this.#configButtonElement.addEventListener("click", this.#onOpenConfigButtonClick);
    }   

    #createElementParts(){
        const scalebar = document.createElement("div");
        scalebar.classList.add(controlName);
    
        const title = document.createElement("h5");
        title.classList.add("title");
        scalebar.appendChild(title);

        const content = document.createElement("div");
        content.classList.add("content");

        const drawArea = document.createElement("canvas");
        content.appendChild(drawArea);
        scalebar.appendChild(content);

        const openConfig = document.createElement("div");
        openConfig.classList.add("openconfigbutton");
        scalebar.appendChild(openConfig);

        const configPanel = document.createElement("div");
        configPanel.classList.add("configpanel");
        configPanel.style.display = "none";
        scalebar.appendChild(configPanel);
        
        return {
            scalebar:scalebar,
            title:title,
            drawArea:drawArea,
            configButton:openConfig,
            configPanel:configPanel
        }
    }

    #onOpenConfigButtonClick=(e:Event)=>{
        
        this.configPanelVisible = !this.#isConfigPanelOpen;
    }


    get configPanelVisible(){ return this.#isConfigPanelOpen;}
    set configPanelVisible(visible:boolean){
        this.#isConfigPanelOpen = visible;
        this.#configPanelElement.style.display = visible ? "" : "none";
    }

    get element(){return this.#element;}
    get canvasElement(){return this.#canvasElement;}
    get configPanelElement(){ return this.#configPanelElement; }

    get configable() {return this.#configable;}
    set configable(enabled:boolean){
        if(this.#configable === enabled) return;
        this.#configable = enabled;

        this.#configButtonElement.style.display = enabled ? "" : "none";
    }

    
}